import React from 'react'
import { Link } from 'react-router-dom'

export default function PrivacyPolicy() {
  return ( <>
  <div className="container text-left mt-4" dir='ltr'>
    
    <div className="row">
        <div className="col-lg-7">
        <h2 className=''>Privacy Policy</h2>
        <p className='m-3'>At Aboney, we are committed to protecting your privacy. We use the information we collect about you to provide and improve our services. This Privacy Policy explains what information we collect, how we use it, and your options regarding our use of the information.</p>

        <h3>Information We Collect:</h3>
        <ul className='m-3'>
            <li>Personally Identifiable Information: We collect information that can be used to identify you, such as your name, email address, and phone number.</li>
            <li>Usage Information: We collect information about how you use our service, such as the pages you visit and the features you use.</li>
        </ul>

        <h3>How We Use Your Information:</h3>
        <ul className='m-3'>
            <li>To provide and improve our service: We use the information we collect to provide and improve our service, including to personalize your experience and to provide customer support.</li>
            <li>To communicate with you: We use your contact information to send you information about our service, such as updates and promotional offers.</li>
        </ul>

        <h3>Sharing of Information:</h3>
        <ul className='m-3'>
            <li>We do not share your information with any third party except as described in this Privacy Policy or with your prior consent.</li>
            <li>We may share your information as required by law, such as to comply with a subpoena or similar legal process.</li>
        </ul>

        <h3>Security:</h3>
        <p className='m-3'>We take reasonable steps to protect the information we collect from loss, misuse, and unauthorized access, disclosure, alteration, and destruction.</p>

        <h3 >Changes to This Privacy Policy:</h3>
        <p className='m-3'>We may update this Privacy Policy from time to time. If we make any material changes, we will provide notice through our service or by other means.</p>

        <h3>Contact Us:</h3>
        <p className='m-3'>If you have any questions or concerns about this Privacy Policy, please contact us at <Link className='text-warning' href="mailto:support@aboney.com">support@aboney.com</Link>.</p>

        <p className='m-3'>By using our service, you consent to our collection, use, and sharing of your information as described in this Privacy Policy.</p>

        </div>
        
    </div>
  </div>
    </>
    
  )
}
