
import { useEffect, useState } from "react";
import { DatePicker, TimePicker } from 'react-rainbow-components';
import LocationMap from "../LocationMap/LocationMap";
import $ from "jquery"
import joi from "joi";
import axios from "axios";
import AboutUserBtn from "../AboutUserBtn/AboutUserBtn";
import { Link, useNavigate } from "react-router-dom";
import Reload from "../Reload/Reload";


function AboneyRequest({getToken, getDifferenceDays, apiUri}) {
    const navigate = useNavigate()
    const [timeDeparture, setTimeDeparture] = useState("01:00")
    const [timeReturn, setTimeReturn] = useState("01:00");
    const [openReturn, setOpenReturn] = useState(false)
    const [startDate, setStartDate] = useState(null)
    const [endDate, setEndDate] = useState(null);
    const [popUp, setPopUp] = useState(false)
    const [addressName, setAddressName] = useState('')
    const [policy, setPolicy] = useState(false)
    const [isLoading, setIsLoading] = useState(false)
    const [errorValidation, setErrorValidation] = useState([]);
    const [nameLabelValidationArray, setNameLabelValidationArray] = useState([])
    const [aboneyRequest, setAboneyRequest] = useState({
        startPoint : '',
        endPoint : "",
        startAboneyDepartureHourly :  "01:00",
        startAboneyReturnHourly : openReturn ?  "00:00" : "0",
        numberInvidules : "1",
        startAboneyDate : "",
        endAboneyDate : "",
        repeatAboney : "يومي",
        repeatAboneyDays : [],
        anotherUser : false,
        suggestedPrice : 400
    })
    function getRepeatAboneyDays(e) {

        // console.log(aboneyRequest);
        // deep copy data
        let myRequest = {...aboneyRequest};
        //update data
        myRequest["repeatAboneyDays"].push(e.target.value)  ;
        // set data
        // console.log(myRequest);
        setAboneyRequest(myRequest);
    } 
    function getAddress() {
        if (localStorage.getItem("address")) {
            const address = localStorage.getItem("address");
            let myRequest = {...aboneyRequest}
            // console.log(address);
            myRequest[addressName] = address
            // console.log(myRequest);
            setAboneyRequest(myRequest)
            localStorage.setItem("address", '')
            // console.log(address);
        }
    }
    function getRequestData(e, typeInput = '', valueInput = null) {
        
        // console.log(aboneyRequest);
        // deep copy
        let myRequest = {...aboneyRequest};
        if (typeInput) {
            // update data 1
            if (typeInput === "startAboneyDate" || typeInput === "endAboneyDate" ) {
                myRequest[typeInput] = valueInput.toString();
            }else {
                myRequest[typeInput] = valueInput;
            }
        }else if (e?.target?.name === "anotherUser") {
            // update data 2
            myRequest[e.target.name] = e.target.checked;
        } else {
            // update data 3
            myRequest[e?.target.name] = e.target.value;
        }
        // set data
        // console.log(myRequest);
        setAboneyRequest(myRequest);
    };
    function validationRequest() {
        const requestSchema = joi.object().required().keys({
            startAboneyDate : joi.string().required(),
            endAboneyDate : joi.string().required(),
            startPoint : joi.string().required() , 
            endPoint : joi.string().required() , 
            startAboneyDepartureHourly : joi.string().regex(/^[0-9]{1,2}:[0-9]{1,2}$/) ,
            startAboneyReturnHourly : joi.string().regex(/(^[0-9]{1,2}:[0-9]{1,2}$ || null)/) ,
            repeatAboney : joi.string().required() , 
            repeatAboneyDays : aboneyRequest.repeatAboney === "أسبوعي" ?  joi.array().min(1).required() : joi.array().required() ,
            numberInvidules : joi.string().alphanum().required() , 
            anotherUser : joi.boolean().required() ,
            suggestedPrice : joi.number().min(1).required()
        }) 
        return requestSchema.validate(aboneyRequest, {abortEarly : false});
    }
    // function getToken() {
    //     let token = localStorage.getItem("token")
    //     return token
    // }
    function validationDate() {
        // console.log('eh');
        const nowDate = new Date().toDateString()
        if (getDifferenceDays(aboneyRequest.startAboneyDate, aboneyRequest.endAboneyDate) < 0 || getDifferenceDays(nowDate, aboneyRequest.startAboneyDate) < 0) {
            setErrorValidation(['يرجي ادخال البيانات بشكل صحيح'])
            return 1;
        }
        return 0;
    }
    async function sendData(e) {
        e.preventDefault();
        setIsLoading(true)
        if (!policy) {
            // console.log(policy);
            setIsLoading(false);
            setErrorValidation(['يجب الموافقة علي شروط الأستخدام'])
            return 0;
        }
        // console.log(validationDate());

        if (validationDate()) {
            // console.log(validationDate());
            setIsLoading(false);
            return 0;
        }
        // const nowDate = new Date().toDateString()
        // if (getDifferenceDays(aboneyRequest.startAboneyDate, aboneyRequest.endAboneyDate) < 0 || getDifferenceDays(nowDate, aboneyRequest.startAboneyDate) < 0) {
        //     setErrorValidation(['يرجي ادخال البيانات بشكل صح يح'])
        //     return 0;
        // }
        const validationError = validationRequest();
        if (validationError.error) {
            // console.log(validationError.error.details);
            setErrorValidation(["يرجي اكمال البيانات بشكل صحيح"])
            handleErrorValidation(validationError.error.details)
        }else {
            setErrorValidation([])
            //Api Call
                    // ** axios way
            const {data} = await axios({
                url : `${apiUri}/api/v1/aboneyRequests/addAboneyRequest?role=riders`,
                method : "post",
                data : aboneyRequest,
                headers : {
                    'Authorization': `Bearer ${getToken()}`
                } ,
            })
            // console.log(data);
            if (data.message  === "success") {
                // setSuccessMessage([data.message]);
                sessionStorage.setItem("successMessage", "true")
                navigate("/about/rider");
            }else if (data.message  === "validation error"){
                setErrorValidation(["يرجي اكمال البيانات بشكل صحيح"])
            }else {
                setErrorValidation([])
            }
        }
        setIsLoading(false);
    }    
    function handleErrorValidation(errors = []) {
        // console.log('2',nameLabelValidationArray);
        let myNameLabelValidationArray = [] 
        nameLabelValidationArray.forEach(ele => {
            if ($(`#${ele}`).hasClass("form-control-border-error") ) {
                $(`#${ele}`).removeClass("form-control-border-error")
                // console.log("why");
            }
        });
        errors.forEach(ele => {
        myNameLabelValidationArray.push(ele.context.label)
            if (!$(`#${ele.context.label}`).hasClass("form-control-border-error") ) {
                if (ele.context.label == "startAboneyDepartureHourly" || ele.context.label == "endAboneyDate" || ele.context.label == "startAboneyDate" || ele.context.label ==  "startAboneyReturnHourly") {
                    $(`.dyBwan`).addClass("form-control-border-error")
                }else {
                    $(`#${ele.context.label}`).addClass("form-control-border-error")
                }
            }
       });
    //    console.log(myNameLabelValidationArray);
       setNameLabelValidationArray(myNameLabelValidationArray)
    }    
    useEffect(() => {
        if (popUp === false) {
            $(".pop-up").addClass("d-none")
        }else
        {
            $(".pop-up").removeClass("d-none")
        }
    
    }, [popUp])
    useEffect(() => {
        if(sessionStorage.getItem('isReload')) {
            sessionStorage.removeItem('isReload')
            window.location.reload()
        }

    }, [])
    
        return ( 

        <>
        {!sessionStorage.getItem('isReload') ? <>
        <section className=" container">

            <form onSubmit={sendData}>
                <div className="row flex-wrap flex-column align-items-center">
                    <div className=" ">
                        <div className="d-flex justify-content-start align-items-baseline">
                            <AboutUserBtn/>
                            {/* <h2 className="text-center my-5 mx-auto header-title">بيانات الطلب</h2> */}
                        </div>
                        
                    </div>
                    <h2 className="text-center mb-5 header-title">بيانات الطلب</h2>

                    <div className="input col-md-5 ">
                    {errorValidation.map((ele, i) => <div key={i} className="alert alert-danger text-center">{ele?.message ? ele.message : ele}</div>)}
                    {/* {successMessage.map((ele, i) => <div key={i} className="alert alert-success text-center">{ele}</div>)}       */}
                            <label htmlFor="startPoint">مكان الأنطلاق</label>
                            <div className="parent-icon">
                                <input type="text" className="form-control" name="startPoint" onChange={getRequestData} onFocus={(e) => {setPopUp(true); setAddressName(e.target.name)}}  value={aboneyRequest.startPoint ? aboneyRequest.startPoint : "" }  id="startPoint" placeholder="مكان الأنطلاق" />
                                <i className="fa-solid fa-location-dot icon"></i>
                            </div>
                        </div>
                        <div className="input col-md-5 ">
                            <label htmlFor="endPoint">مكان الوصول</label>
                            <div className="parent-icon">
                                <input type="text" className="form-control" name="endPoint" id="endPoint" placeholder="مكان الوصول"  onChange={getRequestData} onFocus={(e) =>{ setPopUp(true); setAddressName(e.target.name)}} value={aboneyRequest.endPoint ? aboneyRequest.endPoint : "" } />
                                <i className="fa-solid fa-location-dot icon"></i>
                            </div>
                        </div>
                        <div className="input col-md-5 ">
                            <label htmlFor="startAboneyDepartureHourly">ميعاد الذهاب</label>
                            <div className="parent-icon" >
                            <TimePicker
                            id="startAboneyDepartureHourly"
                            value={timeDeparture}
                            name="startAboneyDepartureHourly"
                            onChange={value => setTimeDeparture(value)} 
                            onFocus = {() => getRequestData('', "startAboneyDepartureHourly", timeDeparture)}
                            className={"hour-dir"}/>
                                <i className="fa-regular fa-clock icon"></i>
                            </div>
                        </div>
                        {/* CHECKBOX */}
                         <div className="input col-md-5 my-1">
                            <input id="open-return" className="ms-1" type="checkbox" onChange={e => setOpenReturn(e.target.checked) } />
                            <label htmlFor="open-return">ميعاد العوده</label>
                        </div>
                        
                        {openReturn ? <>
                        <div className="input col-md-5 ">
                            {/* <label htmlFor="start-hourly">ميعاد العودة</label> */}
                            <div className="parent-icon" >
                            <TimePicker
                            id="startAboneyReturnHourly"
                            value={timeReturn}
                            name="startAboneyReturnHourly"
                            onChange={value => setTimeReturn(value)} 
                            onFocus = {() => getRequestData('', "startAboneyReturnHourly", timeReturn)}
                            className={"hour-dir"}/>
                                <i className="fa-regular fa-clock icon"></i>
                            </div>
                        </div>
                        </> : ""}
                        
                        <div className="input col-md-5 ">
                            <label htmlFor="numberInvidules">عدد الأشخاص بالرحلة</label>
                            <div className="parent-icon">
                                <input type="number" max={5} min={0} className="form-control" name="numberInvidules" id="numberInvidules" placeholder="عدد الأشخاص" defaultValue={1} onChange={getRequestData} />
                                <i className="fa-regular fa-user icon"></i>
                            </div>
                        </div>
                        <div className="input col-md-5 ">
                            <label htmlFor="startAboneyDate">تاريخ بداية الطلب</label>
                            <div className="parent-icon date-picker" >
                                <DatePicker
                                    value={startDate}
                                    name="startAboneyDate"
                                    id="startAboneyDate"
                                    onChange={value => setStartDate(value)}
                                    onFocus={() => startDate ? getRequestData(null, "startAboneyDate", startDate) : ""}
                                    placeholder="تاريخ بداية الطلب"
                                />
                                <i className="fa-regular fa-calendar-plus icon"></i>
                            </div>
                        </div>
                        <div className="input col-md-5  ">
                            <label htmlFor="endAboneyDate">تاريخ نهاية الطلب</label>
                            <div className="parent-icon date-picker" >
                                <DatePicker 
                                value={endDate}
                                name="endAboneyDate"
                                id="endAboneyDate"
                                onChange={(value) => {setEndDate(value)}}
                                onFocus={() => endDate ? getRequestData(null, "endAboneyDate", endDate) : ''}
                                 placeholder="تاريخ نهاية الطلب" />
                                
                                <i className="fa-regular fa-calendar-plus icon"></i>
                            </div>
                        </div>
                        <div className="col-md-5">
                            <p className="mb-0" >التكرار بشكل</p>
                            <div className="row justify-content-between my-2 ">
                                <div className="col-lg-6">
                                    <input type="radio" id="daily" name="repeatAboney" value={"يومي"} onClick={getRequestData} defaultChecked/>
                                    <label htmlFor="daily" className="fs-6 mx-2">يومي</label>
                                </div>
                                <div id="reapeatAboneyDays" className="col-lg-6">
                                    <input type="radio" id="weekly" name="repeatAboney" value={"أسبوعي"} onClick={(e) => {getRequestData(e); } } />
                                    <label htmlFor="weekly" className="fs-6 mx-2">أسبوعي</label>
                                    {aboneyRequest.repeatAboney === "أسبوعي" ? <>
                                            <div className="row">
                                                <div className="col-6 ps-0">
                                                    <input type="checkbox" id="sat" name="repeatAboneyDays" value={"السبت"} onClick={(e) => {getRepeatAboneyDays(e); } } />
                                                    <label htmlFor="sat" className="fs-6 mx-2">السبت</label>
                                                </div>
                                                <div className="col-6 ps-0">
                                                    <input type="checkbox" id="sun" name="repeatAboneyDays" value={"الأحد"} onClick={(e) => {getRepeatAboneyDays(e); } } />
                                                    <label htmlFor="sun" className="fs-6 mx-2">الأحد</label>
                                                </div>
                                                <div className="col-6 ps-0">
                                                    <input type="checkbox" id="mon" name="repeatAboneyDays" value={"الأثنين"} onClick={(e) => {getRepeatAboneyDays(e); } } />
                                                    <label htmlFor="mon" className="fs-6 mx-2">الأثنين</label>
                                                </div>
                                                <div className="col-6 ps-0">
                                                    <input type="checkbox" id="tus" name="repeatAboneyDays" value={"الثلاثاء"} onClick={(e) => {getRepeatAboneyDays(e); } } />
                                                    <label htmlFor="tus" className="fs-6 mx-2">الثلاثاء</label>
                                                </div>
                                                <div className="col-6 ps-0">
                                                    <input type="checkbox" id="wen" name="repeatAboneyDays" value={"الأربعاء"} onClick={(e) => {getRepeatAboneyDays(e); } } />
                                                    <label htmlFor="wen" className="fs-6 mx-2">الأربعاء</label>
                                                </div>
                                                <div className="col-6 ps-0">
                                                    <input type="checkbox" id="thu" name="repeatAboneyDays" value={"الخميس"} onClick={(e) => {getRepeatAboneyDays(e); } } />
                                                    <label htmlFor="thu" className="fs-6 mx-2">الخميس</label>
                                                </div>
                                                <div className="col-6 ps-0">
                                                    <input type="checkbox" id="thu" name="repeatAboneyDays" value={"الجمعة"} onClick={(e) => {getRepeatAboneyDays(e); } } />
                                                    <label htmlFor="thu" className="fs-6 mx-2">الجمعة</label>
                                                </div>
                                            </div>
                                        </> : <></>}
                                </div>

                            </div>
                        </div>
                        <div className="my-4 col-md-5">
                            <input type="checkbox" id="another-user" name="anotherUser" className="bg-danger" onChange={getRequestData} />
                            <label htmlFor="another-user" className="mx-2">السماح لأصطحاب عملاء أخرين</label>
                        </div>
                        <div className="my-4 col-md-5 ps-0">
                            <input type="checkbox" id="policy" name="policy" className="bg-danger" onChange={(e) => setPolicy(e.target.checked)} />
                            <label htmlFor="policy" className="me-2 polict-size">الموافقة علي شروط الأستخدام <Link className="text-warning" to={"/helpCenter/privacyPolicy"}>Privacy Policy</Link> و <Link className="text-warning" to={"/helpCenter/termsService"}>Terms Services</Link></label>
                        </div>
                        <div className="col-12 text-center">
                            <button className=" p-2 px-3 rounded-2 border-0  m-auto my-4 button-submit">أرسال الطلب</button>
                        </div>
                    </div>
                   
            </form>
        </section>
        </> : <Reload/>}
        
        
        <div className="pop-up d-none">
            <div className="container ">
                <LocationMap/>
                <div className="container">
                    <div className="w-50">
                        { isLoading ?  <Reload/> : <button className="confirm-btn" onClick={() => {setPopUp(false);  getAddress()}}>تأكيد</button>  }

                    </div>

                </div>
            </div>
            <div>
                
            </div>
        </div>
        
        </>
    
    );
}

export default AboneyRequest;