import React from 'react'
import { useNavigate } from 'react-router-dom'

export default function LogoutBtn() {
    const navigate = useNavigate()

    function logout() {
        localStorage.clear()
        navigate("/signin")
    }
  return (
    <>
    <i className="fa-solid fa-power-off logout-btn fs-1 d-block" onClick={logout}></i>
    </>
  )
}
