import axios from "axios";
import { useEffect, useState } from "react";
import { useNavigate, useParams } from "react-router-dom";
import { RingLoader } from "react-spinners";
import moment from "moment"
import AboutUserBtn from "../AboutUserBtn/AboutUserBtn";

function AboneyProposals({getToken, getDifferenceDays, coreUnitPrice, apiUri}) {
    const navigate = useNavigate()
    const [aboneyRequest, setAboneyRequest] = useState(null);
    let [price, setPrice] = useState(1);
    const [coreUnit, setCoreUnit] = useState(null)

    let id = useParams()?.id
    let proposalId = useParams().proposalId

    function increment() {
        let newPrice = price +1
        setPrice(newPrice)
    }
    function decrement() {
        let newPrice = price -1
        setPrice(newPrice)
    }
    async function getAboneyRequest() {
        const {data} = await axios({
            url : `${apiUri}/api/v1/aboneyRequests/getAboneyRequest/${id}?role=drivers`,
            method : "get",
            headers : {
                'Authorization' : `Bearer ${getToken()}`
            }
        })
        // console.log(data);
        if (data.result.length) {
            setAboneyRequest(data.result[0]);
            // console.log(data.result[0]);
            setPrice(data.result[0].suggestedPrice);
        }
    }
    async function addProposal() {
        // console.log(price);
        const body = JSON.stringify({
            "price" : `${price}`,
            "aboneyRequestedId": `${id}`
        });
        const {data} = await axios({
            url : `${apiUri}/api/v1/aboneyProposals/addAboneyProposal?role=drivers`,
            method : "post",
            data : body ,
            headers : {
                'Authorization' : `Bearer ${getToken()}`,
                'Content-Type': 'application/json'
            }
        })
        // console.log(data);
        if (data.message === "success add") {
            sessionStorage.setItem("successMessage", "true")
            navigate("/about/driver")
        }
    }
    async function getRequestAndProposal() {
        const {data} = await axios({
            url : `${apiUri}/api/v1/aboneyProposals/getRequestAndProposal?role=drivers&proposalId=${proposalId}`,
            method : "get",
            headers : {
                'Authorization' : `Bearer ${getToken()}`
            }
        })
        //  (data);
        if (data.result) {
            setAboneyRequest(data.result[0]);
            setPrice(data.result[0].price);
        }
    }
    async function editProposal(id, price) {
        const body = JSON.stringify({
          "price" : `${price}`,
          "proposalId": `${id}`
        });
        const {data} = await axios({
          url : "${apiUri}/api/v1/aboneyProposals/editAboneyProposal?role=drivers",
          method : "put",
          data : body ,
          headers : {
            'Authorization' : `Bearer ${getToken()}`,
            'Content-Type': 'application/json'
          }
        });
        if (data.message === "success") {
            sessionStorage.setItem("successMessage", "true")
            navigate("/about/driver")
        }
    }
    // function getDifferenceDays() {
    //     if (aboneyRequest?.startAboneyDate &&  aboneyRequest?.endAboneyDate) {
    //         let start = moment(aboneyRequest.startAboneyDate);
    //         let end = moment(aboneyRequest.endAboneyDate);
    //         let differenceDays = end.diff(start, 'days');
            // console.log( "differenceDays",differenceDays);
    //         coreUnitPrice(differenceDays)
    //     }
    // }
    // function coreUnitPrice() {
    //     if (aboneyRequest?.startAboneyDate &&  aboneyRequest?.endAboneyDate) {
    //         let days = getDifferenceDays(aboneyRequest.startAboneyDate, aboneyRequest.endAboneyDate)
    //         if (days < 28) {
    //             setCoreUnit("المدة")
    //         }else if(days >= 28) {
    //             let weeks = Math.ceil( days / 7)
    //             if (weeks < 8) {
    //                 setCoreUnit("أسبوعين")
    //             }else if (weeks >= 8) {
    //                 setCoreUnit("شهر")
    //             }
    //         }
    //     }
    // }
    useEffect(() => {
        // console.log(id);
        if (proposalId) {
            // console.log('test proposal id');
            getRequestAndProposal()
        }else {
            // console.log('test1');
            getAboneyRequest();
        }
    }, [])
    useEffect(() => {
        if (aboneyRequest) {
            setCoreUnit(coreUnitPrice(getDifferenceDays(aboneyRequest.startAboneyDate, aboneyRequest.endAboneyDate)));
        }
    }, [aboneyRequest])
    
    return ( 
        <>
        
        <section className=" container">
        
        {aboneyRequest ? <>
        <div className="d-flex justify-content-start align-items-center">
            <AboutUserBtn/>
            {/* <h2 className="text-center my-5 mx-auto">بيانات الطلب</h2> */}
        </div>
        <h2 className="text-center mb-5">تقديم عرض</h2>

                <ul className="row flex-wrap flex-column align-items-center">
                    <li className="d-flex col-md-7 mb-2">
                        <i className="fa-solid fa-location-dot icon-proposal"></i>
                        <p className="fw-bolder fixed-width">مكان الأنطلاق : </p>
                        <p className="me-1 fs-location">{aboneyRequest.startPoint} </p>
                    </li>
                    <li className="d-flex col-md-7 mb-2">
                        <i className="fa-solid fa-location-dot icon-proposal"></i>
                        <p className="fw-bolder fixed-width">مكان الوصول : </p>
                        <p className="me-1 fs-location" dir="ltr">{aboneyRequest.endPoint} </p>
                    </li>
                    <li className="d-flex col-md-7 mb-2">
                        <i className="fa-regular fa-clock icon-proposal"></i>
                        <p className="fw-bolder">ميعاد الذهاب : </p>
                        <p className="me-1">{moment(aboneyRequest.startAboneyDepartureHourly).format("LT")} </p>
                    </li>
                    {aboneyRequest.startAboneyReturnHourly != "null" ? <>
                    <li className="d-flex col-md-7 mb-2">
                        <i className="fa-regular fa-clock icon-proposal"></i>
                        <p className="fw-bolder">ميعاد العوده : </p>
                        <p className="me-1"> {moment(aboneyRequest.startAboneyReturnHourly).format("LT")} </p>
                    </li>
                    </> : <li className="d-flex col-md-7 mb-2">
                        <i className="fa-regular fa-clock icon-proposal"></i>
                        <p className="fw-bolder">ميعاد العوده : </p>
                        <p className="me-1"> بلاعودة </p>
                    </li>}
                    <li className="d-flex col-md-7 mb-2">
                        <i className="fa-regular fa-calendar-plus icon-proposal"></i>
                        <p className="fw-bolder">تاريخ بداية الطلب : </p>
                        <p className="me-1">{moment(aboneyRequest.startAboneyDate).format().substring(10,"day")}</p>
                    </li>
                    <li className="d-flex col-md-7 mb-2">
                        <i className="fa-regular fa-calendar-plus icon-proposal"></i>
                        <p className="fw-bolder">تاريخ نهاية الطلب : </p>
                        <p className="me-1">{moment(aboneyRequest.endAboneyDate).format().substring(10,"day")} </p>
                    </li>
                    <li className="d-flex col-md-7 mb-2">
                    <i className="fa-regular fa-user icon-proposal"></i>
                        <p className="fw-bolder">عدد الأشخاص :</p>
                        <p className="me-1">{aboneyRequest.numberInvidules} </p>
                    </li>
                    <li className="d-flex col-md-7 mb-2">
                    <i className="fa-solid fa-repeat icon-proposal"></i>
                        <p className="fw-bolder">تكرار بشكل : </p>
                        <p className="me-1">{aboneyRequest.repeatAboney}<span className="me-2">{aboneyRequest.repeatAboneyDays ? "( " + JSON.parse(aboneyRequest.repeatAboneyDays) + " )" : ''}</span></p>
                    </li>
                    <li className="d-flex col-md-7 mb-2 ">
                    <i className="fa-solid fa-users icon-proposal"></i>
                        <p className=""> {aboneyRequest.anothersUser ? 'يسمح لأصطحاب أشخاص أخرين' : 'لا يسمح لأصطحاب أشخاص أخرين' } </p>
                    </li>
                    <div className="  col-md-7 ">{price < 1 ? <p className="alert alert-danger">لايمكن ان يكون عرضك اقل من 1 جنية !</p> : ''}</div>

                <div className="card-order col-md-7 mb-0">
                    <div className="d-flex justify-content-between align-items-center  bg-card-order shadow-lg p-3">
                        <div className="icon-order" onClick={increment}>
                            <i className="fa-solid fa-plus" ></i>
                        </div>
                        <div className="text-center fs-4" >
                            <input className="price-input text-center m-0" type="number" min={1} value={price} onChange={(e) => setPrice(parseInt(e.target.value))} />
                            <p className="text-muted m-0">جنية / المدة كاملة</p>
                        </div>
                        
                        <div className="icon-order" onClick={decrement}>
                            <i className="fa-solid fa-minus"></i>
                        </div>
                    </div>
                    <div className="col-md-4 bg-card-footer-order">
                        <div className="text-end">{coreUnit === "المدة" ?  <p className="bg-MyPrimary text-MyBase m-0 pe-2 pb-1 driver-pay">- يتم صرف الدفعات بالكامل في نهاية المدة</p> : <p className="bg-MyPrimary text-MyBase m-0 pb-1 pe-2 driver-pay">- يتم صرف الدفعات بشكل متكرر كل مدة لا تقل عن  ( اسبوعين - شهر ) بما يتناسب  مع الطلب</p> }</div>            
                        <button className="bg-order p-2 text-center button-submit fs-5" onClick={ () => proposalId ? editProposal(proposalId, price) : addProposal()} >تقديم العرض</button>
                    </div>
                    {/* اقل من 28 يوم  */}
                    <p className="driver-compassion m-2">* يتم خصم 5% من اجمالي المبلغ لكل <span>{coreUnit}</span> </p>
                </div>

                    
                    
                </ul>                
        </> : <><RingLoader className="mx-auto" color="#cf9400" /></>}
        </section>
        </>
        
     );
}

export default AboneyProposals;