import React from 'react'
import avatar from "../../img/avatar.png"

import { useNavigate } from 'react-router-dom'

export default function AboutUserBtn() {
    const navigate = useNavigate()

    function navigateAboutUser() {
        const role = localStorage.getItem("roleUser")
        if (role === "riders") {
            navigate("/about/rider")
        }else if(role === "drivers"){
            navigate("/about/driver")
        }
    }
  return (
  <>
    <div className='text-sm-center my-3 profile-icon'><img className='w-100 rounded-circle' src={avatar} alt="" onClick={navigateAboutUser}/></div>

  </>
  )
}
