import appLogo from "../../img/aboneyAppLogo.png";

function Intro() {

    return ( 
        
        <div className="bg-MyPrimary d-flex justify-content-center align-items-center vh-100 ">
            <div className=" animate__animated animate__fadeIn animate_faster ">
            <img src={appLogo} className="intro-logo-size animate__animated animate__delay-1s animate__shakeY  animate__slow" alt="logo Aboney" />
            </div>
            
        </div>
     );
}

export default Intro;