import React, { createContext, useState } from 'react'
import  axios from 'axios';
export const apiAboutUserContext = createContext(0);

export default function ApiAboutUserProvider(props) {

  const [AboutUser, setAboutUser] = useState(null)

  // function getToken() {
  //   let token = localStorage.getItem("token")
  //   return token
  // }
  // function getRoleUser() {
  //   let roleUser = JSON.parse(localStorage.getItem("roleUser"))
  //   console.log(roleUser);
  //   return roleUser
  // }

  async function getAboutApi() {
    const role = props.getRoleUser()
    // console.log(role);
    const {data} = await axios({
      url : `${props.apiUri}/api/v1/${role}?role=${role}`,
      // url : `http://localhost:3000/api/v1/drivers?role=drivers`,
      method : "get",
      headers : {
        'Authorization' : `Bearer ${props.getToken()}`
      }
    })
    // console.log(data);
    if (data.message === "success") {
      setAboutUser(data.result[0])
      // console.log(data.result[0]);
    }else {
      setAboutUser([])
    }
  }  

  

    return  <apiAboutUserContext.Provider value= {{getAboutApi, AboutUser}}>
                {props.children}
            </apiAboutUserContext.Provider>
}
