import avatar from "../../img/avatar.png"
import Slider from "react-slick";
import HistoryRequests from "../HistoryRequests/HistoryRequests";
import RequestAboneyBtn from "../RequestAboneyBtn/RequestAboneyBtn";
import { apiAboutUserContext } from "../Store/ApiAboutUserContext";
import { useContext, useEffect, useState } from "react";
import { RingLoader } from "react-spinners";
import axios from "axios";
import LogoutBtn from "../LogoutBtn/LogoutBtn";
import $ from 'jquery'
import { Link } from "react-router-dom";
import Reload from "../Reload/Reload";

function AboutRide({getToken, getRoleUser, getDifferenceDays, coreUnitPrice, apiUri}) {

  const responsiveCarousalHistoryProposals = [
    {
        breakpoint : 1024,
        settings : {
            slidesToShow : 1,
            centerMode : false,
            centerPadding : "30px"

        },
    },
    {
        breakpoint : 600,
        settings : {
            slidesToShow : 1,
            centerMode : true,
            centerPadding : "30px"

        },
    },
    {
        breakpoint : 480,
        settings : {
            slidesToShow : 1,
            centerMode : false,
        },
    }
]
const {getAboutApi, AboutUser} = useContext(apiAboutUserContext);
const [myProposals, setMyProposals] = useState([]);
const [refresh, setRefresh] = useState(0);
const [refreshHistory, setRefreshHistory] = useState(0);
const [isAcceptProposal, setIsAcceptProposal] = useState(false)
const [infoProposal, setInfoProposal] = useState({});
const [coreUnit, setCoreUnit] = useState('');
const [successMessage, setSuccessMessage] = useState(false);
// const []





async function getLastProposal() {
      let {data} = await axios.get(`${apiUri}/api/v1/aboneyProposals/getAboneyProposal?role=riders`, {
      headers : {
        'Authorization' : `Bearer ${getToken()}`
      }
    })
    // console.log(data);
    if (data.message === "success") {
      // console.log("test1", data.result);
      setMyProposals(data.result);
    }else {
      // console.log('test2', data.result);
      setMyProposals([]);
    }
}
  function confirmAcceptProposal(finalPrice, driverId, requestId, proposalId, days, coreUnit) {
    // console.log(finalPrice, driverId, requestId, proposalId, days, coreUnit);
      $(".pop-up").removeClass("d-none");
    let infoProposal = {
      finalPrice,
      driverId,
      requestId,
      proposalId,
      days,
      coreUnit,
      balance : validationBalance(finalPrice, coreUnit, days)
    };
    // const unitPrice = coreUnitPrice(days);
    setCoreUnit(coreUnit);
    setInfoProposal(infoProposal);
    // sessionStorage.setItem("infoProposal", JSON.stringify(infoProposal));
  }
  function validationBalance(finalPrice, coreUnit, days) {
    // console.log(finalPrice, coreUnit, days);

    if (coreUnit === "المدة") {
      if (Math.ceil(finalPrice * .1) <= AboutUser.balance) {
        
        $("#acceptProposal").removeClass(" pe-none opacity-50")
        return AboutUser.balance - Math.ceil(finalPrice * .1)
      }else {
        $("acceptProposal").hasClass("pe-none opacity-50") ? $("#acceptProposal").removeClass(" pe-none opacity-50") : $("#acceptProposal").addClass(" pe-none opacity-50") 
      }
    } else {
      if ( coreUnit === "شهر" ) {
        if (Math.ceil((finalPrice / (Math.ceil( days / 7)))) * 4 <= AboutUser.balance) {
          // console.log($("acceptProposal").hasClass("pe-none opacity-50"));
          $("#acceptProposal").removeClass(" pe-none opacity-50")
          return AboutUser.balance - Math.ceil((finalPrice / (Math.ceil( days / 7)))) * 4
        }else {
          $("acceptProposal").hasClass("pe-none opacity-50") ? $("#acceptProposal").removeClass(" pe-none opacity-50") : $("#acceptProposal").addClass(" pe-none opacity-50")
        }
        
      }else if (coreUnit === "أسبوعين") {
        if (Math.ceil((finalPrice / (Math.ceil( days / 7)))) * 2 <= AboutUser.balance ) {
          $("#acceptProposal").removeClass(" pe-none opacity-50")
          return AboutUser.balance - Math.ceil((finalPrice / (Math.ceil( days / 7)))) * 2 
        } else {
          $("acceptProposal").hasClass("pe-none opacity-50") ? $("#acceptProposal").removeClass(" pe-none opacity-50") : $("#acceptProposal").addClass(" pe-none opacity-50")
        }
      } 
    }
  }
async function acceptProposal(finalPrice, days, driverId, requestId, proposalId, balance) {
  const body = JSON.stringify({
    "finalPrice" : `${finalPrice}`,
    "acceptedDriverId" : `${driverId}`,
    "requestId" : `${requestId}`,
    "proposalId" : `${proposalId}`,
    "days" : `${days}`,
    "balance" : `${balance}`
  });
  const {data} = await axios({
      url : `${apiUri}/api/v1/aboneyProposals/acceptedProposal?role=riders`,
      method : "post",
      data : body ,
      headers : {
        'Authorization' : `Bearer ${getToken()}`,
        'Content-Type': 'application/json',
      }
  })
  // console.log(data);
  if (data.message === "success") {
    getLastProposal();
    setRefresh(1);
    const numRefreshHistory = refreshHistory +1
    setRefreshHistory(numRefreshHistory);
    getAboutApi()
  }else {
    // console.log(data);
  }
}
async function refuseProposal(proposalId) {
  const body = JSON.stringify({
    "proposalId" : `${proposalId}`  
  });
  const {data} = await axios({
      url : `${apiUri}/api/v1/aboneyProposals/refusedProposal?role=riders`,
      method : "put",
      data : body ,
      headers : {
        'Authorization' : `Bearer ${getToken()}`,
        'Content-Type': 'application/json'
      }
  })
  // console.log(data);
  if (data.message === "success") {
    getLastProposal()
    let numRefreshHistory = refreshHistory + 1
    setRefreshHistory(numRefreshHistory);
  }
}
function handlePopUp(e) {
  // console.log(e);  
  if (e.target.attributes.data_accept) {
    $(".pop-up").addClass("d-none")
    if (e.target.attributes.data_accept?.value !== "accept") {
      // sessionStorage.removeItem("infoProposal")
      setInfoProposal({})
    }
  }
}
function displaySuccessMessage() {
  $(".successMessage").removeClass("d-none")
  sessionStorage.removeItem("successMessage")
  setTimeout( () => {
  //  $(".successMessage").addClass("d-none");
}, 3500);      
}

useEffect(() => {
  getAboutApi()
  getLastProposal()
  // console.log(myProposals);
  if (sessionStorage.getItem("successMessage")) {
    displaySuccessMessage()
  }
}, []);

useEffect(() => {
  // console.log(isAcceptProposal);
  if (isAcceptProposal) {
    // console.log('haaaa3 1');
    // console.log(isAcceptProposal);
    const {finalPrice, driverId, requestId, proposalId, days, balance} = infoProposal;
    acceptProposal(finalPrice, days, driverId, requestId, proposalId, balance);
    // sessionStorage.removeItem("infoProposal")
    setIsAcceptProposal(false)
    // console.log(isAcceptProposal);
    setInfoProposal({})
  }else {
    // console.log('haaaa3 2');
  }
}, [isAcceptProposal])

useEffect(() => {
  if (refresh === 1) {
    // console.log("riderRefresh");
    // getLastProposal()
    setRefresh(0)
  }
}, [refresh])


    return (
      <>
      
      {AboutUser ? <>
        <div className="container mt-3">
          {/* S T A R T   I N F O    R I D E R  */}

          <div className="row justify-content-md-center">
            
            <div className="col-sm-3 col-3">
              <div className="avatar">
                <img
                  className="img-avatar rounded-circle w-100"
                  src={avatar}
                  alt=""
                />
              </div>
            </div>
            <div className="mb-2 text-xl-end text-center col-9 d-md-none d-flex align-items-center  justify-content-between ">
              <div className="d-flex flex-column ">
                <h2 className=" fs-1 my-3">{AboutUser ? AboutUser.firstName + " " + AboutUser.lastName : '' }</h2>
                <p className="text-end text-money-wallet"><span>{AboutUser ? AboutUser.balance : ""}</span> جنية</p>
                
              </div>
              <div className="d-md-none">
                <LogoutBtn/>
                {/* <i class="fa-solid fa-money-bill-transfer fs-2 icon-wallet d-block mt-3"></i> */}
                <Link className="icon-wallet-parrent" to={"/wallet/deposit"}>
                  <svg className="icon-wallet-parrent my-2" xmlns="http://www.w3.org/2000/svg" version="1.0" width="32.000000pt" height="32.000000pt" viewBox="0 0 32.000000 32.000000" preserveAspectRatio="xMidYMid meet">
                  <g transform="translate(0.000000,32.000000) scale(0.100000,-0.100000)"  className="icon-wallet" stroke="none">
                  <path d="M95 295 c-14 -13 -25 -31 -25 -38 0 -8 -11 -22 -25 -31 -23 -15 -25 -22 -25 -102 0 -126 -2 -124 136 -124 100 0 115 2 120 18 3 9 10 25 15 34 12 23 12 65 -1 73 -5 3 -10 15 -10 26 0 11 -7 22 -15 25 -8 4 -15 17 -15 30 0 13 -4 24 -10 24 -5 0 -16 15 -24 34 -25 59 -80 73 -121 31z m81 -46 c18 -7 18 -8 -3 -14 -12 -4 -23 -13 -25 -21 -2 -9 -10 -6 -27 11 -17 18 -19 25 -8 25 8 0 18 10 21 23 7 21 8 21 15 3 4 -11 16 -23 27 -27z m-90 -67 c-10 -2 -24 -1 -33 2 -12 5 -13 9 -3 21 11 14 15 14 33 -2 17 -16 17 -18 3 -21z m144 13 c0 -9 -9 -15 -21 -15 -18 0 -19 2 -9 15 7 8 16 15 21 15 5 0 9 -7 9 -15z m30 -48 c0 -7 -10 -17 -22 -22 -15 -7 -23 -19 -23 -35 0 -16 8 -28 23 -35 12 -5 22 -16 22 -23 0 -10 -25 -12 -108 -10 l-107 3 -3 68 -3 67 111 0 c82 0 110 -3 110 -13z m0 -57 c0 -5 -4 -10 -10 -10 -5 0 -10 5 -10 10 0 6 5 10 10 10 6 0 10 -4 10 -10z"/>
                  <path d="M65 50 c-4 -6 12 -10 39 -10 25 0 46 5 46 10 0 13 -77 13 -85 0z"/>
                  </g>
                  </svg>
                        </Link>
                {/* <p className="text-wallet">شحن</p> */}

              </div>
              
            </div>            
            <div className="col-xl-8 col-md-7">
              <div className="row">
                <div className="mb-2 text-end d-md-inline-block d-none">
                  <div className="d-flex justify-content-between">
                    <div className="d-flex flex-column">
                      <h2 className=" fs-1 my-3">{AboutUser ? AboutUser.firstName + " " + AboutUser.lastName : '' }</h2>
                      <p className="text-end text-money-wallet"><span>{AboutUser ? AboutUser.balance : ""}</span> جنية</p>
                    </div>
                    <div className="icons ">
                      <div className="text-start d-md-block d-none">
                        <LogoutBtn/>
                      </div>
                      <div className="text-start d-md-inline-block d-none mt-3">
                        {/* <i class="fa-solid fa-money-bill-transfer icon-wallet fs-2 d-block"></i> */}
                        {/* <p className="text-wallet"><span className="">شحن</span> </p> */} 
                        <Link className="icon-wallet-parrent" to={"/wallet/deposit"}>
                          <svg className="icon-wallet-parrent" xmlns="http://www.w3.org/2000/svg" version="1.0" width="32.000000pt" height="32.000000pt" viewBox="0 0 32.000000 32.000000" preserveAspectRatio="xMidYMid meet">
                          <g transform="translate(0.000000,32.000000) scale(0.100000,-0.100000)"  className="icon-wallet" stroke="none">
                          <path d="M95 295 c-14 -13 -25 -31 -25 -38 0 -8 -11 -22 -25 -31 -23 -15 -25 -22 -25 -102 0 -126 -2 -124 136 -124 100 0 115 2 120 18 3 9 10 25 15 34 12 23 12 65 -1 73 -5 3 -10 15 -10 26 0 11 -7 22 -15 25 -8 4 -15 17 -15 30 0 13 -4 24 -10 24 -5 0 -16 15 -24 34 -25 59 -80 73 -121 31z m81 -46 c18 -7 18 -8 -3 -14 -12 -4 -23 -13 -25 -21 -2 -9 -10 -6 -27 11 -17 18 -19 25 -8 25 8 0 18 10 21 23 7 21 8 21 15 3 4 -11 16 -23 27 -27z m-90 -67 c-10 -2 -24 -1 -33 2 -12 5 -13 9 -3 21 11 14 15 14 33 -2 17 -16 17 -18 3 -21z m144 13 c0 -9 -9 -15 -21 -15 -18 0 -19 2 -9 15 7 8 16 15 21 15 5 0 9 -7 9 -15z m30 -48 c0 -7 -10 -17 -22 -22 -15 -7 -23 -19 -23 -35 0 -16 8 -28 23 -35 12 -5 22 -16 22 -23 0 -10 -25 -12 -108 -10 l-107 3 -3 68 -3 67 111 0 c82 0 110 -3 110 -13z m0 -57 c0 -5 -4 -10 -10 -10 -5 0 -10 5 -10 10 0 6 5 10 10 10 6 0 10 -4 10 -10z"/>
                          <path d="M65 50 c-4 -6 12 -10 39 -10 25 0 46 5 46 10 0 13 -77 13 -85 0z"/>
                          </g>
                          </svg>
                        </Link>
                        
                      </div>
                    </div>
                    
                  </div>
                  
                </div>
                

                <ul className="col-12 pe-4">
                <li className="d-flex mb-2">
                    <i className="fa-solid fa-globe icon-about"></i>
                    <p className="fs-6 me-1">{AboutUser ? AboutUser.city : '' } </p>
                  </li>
                  
                  <li className="d-flex mb-2">
                    <i className="fa-solid fa-phone icon-about"></i>
                    <p className="fs-6 me-1">{AboutUser ? AboutUser.phone : ""} </p>
                  </li>
                  <li className="d-flex mb-2">
                  <i className="fa-regular fa-envelope icon-about"></i>
                    <p className="fs-6 me-1">{AboutUser ? AboutUser.email : ""}</p>
                  </li>
                  
                  
                </ul>
              </div>
            </div>
          </div>
          {/* E N D   I N F O    R I D E R  */}

          <hr className="line-horizontal" />
          
          {/* S T A R T    H I S T O R Y   P R O P O S A L S */}

          <section className="mb-5 container px-4 position-relative" id="history-proposals" >
            < div className="alert alert-success p-2 d-none successMessage d-flex animate__animated animate__fadeIn animate__delay-2s animate__fadeOut" role="alert">
              <i class="fa-regular fa-circle-check ms-2"></i>
              <p className="m-0">تم بنجاح</p>
            </div>
            <h2 className="mt-5 mb-3">أخر العروض</h2>
            {myProposals.length ? <>
              <Slider
            className="center"
            arrows={true}
            centerMode={false}
            infinite={true}
            slidesToShow={1}
            speed={600}
            dots={true}
            draggable={false}
            touchMove={false}

            responsive={[
              {
                  breakpoint : 1024,
                  settings : {
                      slidesToShow : 1,                      
                  },
              },
              {
                  breakpoint : 600,
                  settings : {
                      slidesToShow : 1,
                  },
              },
              {
                  breakpoint : 480,
                  settings : {
                      slidesToShow : 1,
                  },
              }
          ]}
            >
              {myProposals?.length ? myProposals?.map((ele, index) => {
                return <>
                 <div  key={index} className="p-2">
              <div className="bg-MyPrimary px-4 py-3 rounded-4">
                <div className="text-end me-4 my-2">
                  {myProposals.length !== 1 ? <>
                    <p className="fs-6 location-1-coursol mb-4 ">{ele[0].startPoint?.split(",")[1]?.length > 20 ?".."+ele[0].startPoint?.split(",")[1]?.slice(1,20) : ele[0].startPoint?.split(",")[1]}</p>
                  <p className="fs-6 location-2-coursol mt-4">{ele[0].endPoint?.split(",")[1]?.length > 20 ?".."+ele[0].endPoint?.split(",")[1]?.slice(1,20)  : ele[0].endPoint?.split(",")[1]}</p>
                    
                  </> : <>
                  <p className="fs-6 location-1-coursol mb-4 ">{ele[0].startPoint?.split(",")[1]?.length > 20 ?ele[0].startPoint?.split(",")[1]?.slice(1,20)+".." : ele[0].startPoint?.split(",")[1]}</p>
                  <p className="fs-6 location-2-coursol mt-4">{ele[0].endPoint?.split(",")[1]?.length > 20 ?ele[0].endPoint?.split(",")[1]?.slice(1,20)+".."  : ele[0].endPoint?.split(",")[1]}</p>
                  </>}
                  
                </div>
                <div className="container">
              <Slider
                className="center"
                arrows={true}
                centerMode={true}
                infinite={true}
                centerPadding="100px"
                slidesToShow={1}
                speed={600}
                dots={false}              
                responsive={responsiveCarousalHistoryProposals}
               
              >{ele?.map((ele2, i) => {
                return <>
                  <div  key={i} className="p-2">
                  
                  <div className="d-flex justify-content-between align-items-center px-3 rounded-4 card-proposal" dir={ele2.length !== 1 ? "ltr" : ""}>
                    <div className="">
                      
                      <div className="text-center text-MyBase" dir="rtl">
                      <span className="" > { ele2.coreUnit === 'المدة' ?  Math.ceil(ele2.price * 0.1) + " جنيه / المدة" : ele2.coreUnit === "شهر" ? Math.ceil((ele2.price / (Math.ceil( ele2.numDays / 7)))) * 4 + " جنيه / شهر" : Math.ceil((ele2.price / (Math.ceil( ele2.numDays / 7)))) * 2 + " جنيه / اسبوعين" }</span> 
                      </div>
                      
                      <div className="d-flex buttons-proposal ">
                        <button className=" bg-MySecondry px-sm-4 py-sm-1 p-1 m-2" onClick={() => confirmAcceptProposal(ele2.price, ele2.driverId, ele2.requestId, ele2.proposalId, ele2.numDays, ele2.coreUnit)}>
                          قبول
                        </button>
                        <button className="bg-MySecondry px-sm-4 py-sm-1 p-1 m-2" onClick={() => refuseProposal(ele2.proposalId)}>
                          رفض
                        </button>
                      </div>
                    </div>
                    <div className="d-md-flex">
                          <div>
                              <p className=" mt-3 text-center text-MyBase d-md-inline-block d-none">{ele2.firstName + " " + ele2.lastName}</p>
                          </div>
                          <img
                          className=" rounded-circle p-2 img-size-history-proposal"
                          src={avatar}
                          alt=""
                          />
                          <div className="text-center">
                              <p className="m-3 text-MyBase d-md-none d-inline-block p-xs-2">{ele2.firstName + " " + ele2.lastName}</p>
                          </div>
                      </div>

                  </div>
                </div>
                </>
              })}
            </Slider>
                </div>
              </div>
            </div>

                </>
              }) : ''}
           

            </Slider>
            </> : <><div className='text-center mt-5'><p className='fs-2 '> لا يوجد هنا عروض </p></div></>}
            
          </section>
          {/* E N D     H I S T O R Y   P R O P O S A L S */}

          {/* S T A R T    H I S T O R Y    R E Q U  E S T S  */}
          <section className="container" id="history-requests">
            <h2 className="mt-5">أخر الطلبات</h2>
            <HistoryRequests getToken={getToken} getRoleUser={getRoleUser} setRefreshRider={setRefresh} refreshHistory={refreshHistory}/>
          </section>
          
         {/* E N D   H I S T O R Y   R E Q U  E S T S   */}
        </div>
        <div className="pop-up d-none" onClick={handlePopUp} data_accept="refuse">
          <div className="bg-light light-box p-2  rounded-4 " >
          <i data_accept="refuse" className="fa-regular fa-circle-xmark fs-5 cursor-pointer"></i>
            <ul className="p-3">
              <li className="m-2 text-center fw-bolder fs-5"> تكلفة الطلب كاملاً <span>{infoProposal.finalPrice}</span> جنية.</li>
              {coreUnit === "المدة" ? <>
              <li className="m-2">- حتي يكون بإمكانك قبول العرض يرجي شحن حسابك بقيمة لا تقل عن ( <span>{Math.ceil(infoProposal.finalPrice * .1)}</span> جنية ) من إجمالي قيمة الطلب.</li>
              <li className="m-2">- لابد من شحن حسابك بإجمالي قيمة الطلب قبل مرور <span>{Math.floor(infoProposal.days * 0.1) === 0 ? 1 : Math.floor(infoProposal.days * 0.1)  }</span> يوم من بدء الطلب للحفاظ علي عدم إيقاف الطلب.</li>
              </> : <>
              <li className="m-2">- حتي يكون بإمكانك قبول العرض يرجي شحن حسابك بقيمة {coreUnit} ( <span>{ coreUnit === "شهر" ? Math.ceil((infoProposal.finalPrice / (Math.ceil( infoProposal.days / 7)))) * 4 : Math.ceil((infoProposal.finalPrice / (Math.ceil( infoProposal.days / 7)))) * 2 }</span> جنية )  من إجمالي قيمة الطلب.</li>
              <li className="m-2">- يرجي الحفاظ علي حسابك مشحون بقيمة لا تقل عن <span>( { coreUnit === "شهر" ? Math.ceil((infoProposal.finalPrice / (Math.ceil( infoProposal.days / 7)))) * 4 : Math.ceil((infoProposal.finalPrice / (Math.ceil( infoProposal.days / 7)))) * 2 } جنية )</span> كل {coreUnit} حتي لا يتم ايقاف طلبك.</li>
              </>}
              
            </ul>
            <div className="text-center">
              <button id="acceptProposal" data_accept="accept"  className="btn bg-success btn-success mb-2" onClick={() => setIsAcceptProposal(true)}>تأكيد</button>
            </div>
          </div>
        </div>
        <RequestAboneyBtn />


        
        
      </> : <Reload/> }
      </>
    );
}

export default AboutRide;