import axios from "axios";
import { useEffect, useContext, useState } from "react";
import { Link } from "react-router-dom";
import Slider from "react-slick";
import { RingLoader } from "react-spinners";
import avatar from "../../img/avatar.png"
import HistoryRequests from "../HistoryRequests/HistoryRequests";
import LocationMap from "../LocationMap/LocationMap";
import LogoutBtn from "../LogoutBtn/LogoutBtn";
import { apiAboutUserContext } from "../Store/ApiAboutUserContext";
import $ from 'jquery';

function AboutDriver( {getToken, getRoleUser, apiUri} ) {
  const {getAboutApi, AboutUser} = useContext(apiAboutUserContext)
  const [allRequestsAvailable, setAllRequestsAvailable] = useState([]);
  const [refresh, setRefresh] = useState(0)

  async function getAllAboneyRequestsAvailable() {
      const {data} = await axios({
        url : `${apiUri}/api/v1/aboneyRequests/getAboneyRequestsAvailable?role=drivers`,
        method : "get",
        headers : {
          'Authorization' : `Bearer ${getToken()}`
        }
      })
      // console.log(data);
      if (data.result) {
        setAllRequestsAvailable(data.result)
      }
     
  }
  function displaySuccessMessage() {
    $(".successMessage").removeClass("d-none")
    sessionStorage.removeItem("successMessage")
    setTimeout( () => {
     $(".successMessage").addClass("d-none");
  }, 3500);      
  }
  useEffect(() => {
    getAboutApi()
    getAllAboneyRequestsAvailable()
    if (sessionStorage.getItem("successMessage")) {
      displaySuccessMessage();
    }
  }, [])
  useEffect(() => {
    if (refresh == 1) {
      getAllAboneyRequestsAvailable()
      setRefresh(0)
    }

  }, [refresh])
  
      
    return (
      <>

        <div className="container mt-3">
          {/* S T A R T   I N F O  D R I V E R  */}
          {AboutUser ? <>
            <div className="row justify-content-md-center">
            <div className="col-sm-3 col-3">
              <div className="avatar">
                <img
                  className="img-avatar rounded-circle w-100"
                  src={avatar}
                  alt=""
                />
              </div>
            </div>
            <div className="mb-2 text-xl-end text-center col-9 d-md-none d-flex justify-content-between align-items-center ">
              <div className="d-flex flex-column">
                  <h2 className=" fs-1 my-3">{AboutUser.firstName ? AboutUser.firstName + " " + AboutUser.lastName : '' }</h2>
                  <p className="text-end text-money-wallet"><span>0.00</span> جنية</p>
                </div>
              <div className="d-md-none">
                <LogoutBtn/>
                {/* <i class="fa-solid fa-sack-dollar fs-2 icon-wallet d-block mt-3"></i>
                <p className="text-wallet">سحب</p> */}
                <div className="icon-wallet-parrent mt-3">
                  <svg xmlns="http://www.w3.org/2000/svg" version="1.0" width="32.000000pt" height="32.000000pt" viewBox="0 0 32.000000 32.000000" preserveAspectRatio="xMidYMid meet">
                    <g className="icon-wallet" transform="translate(0.000000,32.000000) scale(0.100000,-0.100000)" stroke="none">
                    <path d="M29 260 c-6 -61 6 -90 26 -68 8 9 10 30 4 68 l-7 55 1 -58 c1 -33 -2 -57 -8 -57 -6 0 -9 17 -7 39 2 21 2 47 0 57 -3 11 -7 -6 -9 -36z"/>
                    <path d="M70 290 c0 -11 5 -20 10 -20 14 0 13 -47 -2 -73 -7 -12 -13 -59 -14 -104 l-1 -83 49 0 c48 0 49 1 48 27 -6 83 -3 92 38 95 l37 3 3 68 c2 37 7 67 12 67 6 0 10 9 10 20 0 19 -7 20 -95 20 -88 0 -95 -1 -95 -20z m150 -45 c0 -45 -4 -58 -25 -80 -14 -14 -27 -25 -29 -25 -2 0 -7 12 -10 26 -5 20 -12 25 -26 21 -18 -5 -20 0 -20 54 l0 59 55 0 55 0 0 -55z m-108 -113 c12 -2 18 5 20 25 3 23 5 19 12 -19 11 -61 4 -78 -32 -78 -28 0 -30 3 -36 49 -7 56 5 107 14 56 3 -17 12 -31 22 -33z m108 23 c0 -8 -7 -15 -15 -15 -9 0 -12 6 -9 15 4 8 10 15 15 15 5 0 9 -7 9 -15z m-70 -120 c0 -11 -11 -15 -40 -15 -29 0 -40 4 -40 15 0 11 11 15 40 15 29 0 40 -4 40 -15z"/>
                    <path d="M140 276 c-27 -33 18 -72 48 -42 13 13 14 20 6 34 -15 23 -38 26 -54 8z m48 -19 c4 -20 -25 -34 -40 -19 -15 15 -1 44 19 40 10 -2 19 -11 21 -21z"/>
                    <path d="M259 190 c-6 -61 6 -90 26 -68 8 9 10 30 4 68 l-7 55 1 -58 c1 -33 -2 -57 -8 -57 -6 0 -9 17 -7 39 2 21 2 47 0 57 -3 11 -7 -6 -9 -36z"/>
                    </g>
                  </svg>
                </div>
              </div>
            </div>
            <div className="col-md-7 ">
              <div className="row justify-content-md-center justify-content-start">
                <div className="mb-2 text-end d-md-inline-block d-none">
                  <div className="d-flex justify-content-between">
                    <div className="d-flex flex-column">
                      <h2 className=" fs-1 my-3">{AboutUser.firstName ? AboutUser.firstName + " " + AboutUser.lastName : '' }</h2>
                      <p className="text-end text-money-wallet"><span>0.00</span> جنية</p>
                    </div>
                    <div className="icons ">
                      <div className="text-start d-md-block d-none">
                        <LogoutBtn/>
                      </div>
                      <Link className="icon-wallet-parrent text-start d-md-inline-block d-none mt-3" to={"/wallet/withdrawal"}>
                        <svg xmlns="http://www.w3.org/2000/svg" version="1.0" width="32.000000pt" height="32.000000pt" viewBox="0 0 32.000000 32.000000" preserveAspectRatio="xMidYMid meet">
                          <g className="icon-wallet" transform="translate(0.000000,32.000000) scale(0.100000,-0.100000)" stroke="none">
                          <path d="M29 260 c-6 -61 6 -90 26 -68 8 9 10 30 4 68 l-7 55 1 -58 c1 -33 -2 -57 -8 -57 -6 0 -9 17 -7 39 2 21 2 47 0 57 -3 11 -7 -6 -9 -36z"/>
                          <path d="M70 290 c0 -11 5 -20 10 -20 14 0 13 -47 -2 -73 -7 -12 -13 -59 -14 -104 l-1 -83 49 0 c48 0 49 1 48 27 -6 83 -3 92 38 95 l37 3 3 68 c2 37 7 67 12 67 6 0 10 9 10 20 0 19 -7 20 -95 20 -88 0 -95 -1 -95 -20z m150 -45 c0 -45 -4 -58 -25 -80 -14 -14 -27 -25 -29 -25 -2 0 -7 12 -10 26 -5 20 -12 25 -26 21 -18 -5 -20 0 -20 54 l0 59 55 0 55 0 0 -55z m-108 -113 c12 -2 18 5 20 25 3 23 5 19 12 -19 11 -61 4 -78 -32 -78 -28 0 -30 3 -36 49 -7 56 5 107 14 56 3 -17 12 -31 22 -33z m108 23 c0 -8 -7 -15 -15 -15 -9 0 -12 6 -9 15 4 8 10 15 15 15 5 0 9 -7 9 -15z m-70 -120 c0 -11 -11 -15 -40 -15 -29 0 -40 4 -40 15 0 11 11 15 40 15 29 0 40 -4 40 -15z"/>
                          <path d="M140 276 c-27 -33 18 -72 48 -42 13 13 14 20 6 34 -15 23 -38 26 -54 8z m48 -19 c4 -20 -25 -34 -40 -19 -15 15 -1 44 19 40 10 -2 19 -11 21 -21z"/>
                          <path d="M259 190 c-6 -61 6 -90 26 -68 8 9 10 30 4 68 l-7 55 1 -58 c1 -33 -2 -57 -8 -57 -6 0 -9 17 -7 39 2 21 2 47 0 57 -3 11 -7 -6 -9 -36z"/>
                          </g>
                        </svg>
                      </Link>
                    </div>
                  </div>
                </div>

                <ul className="col-6 pe-4">
                <li className="d-flex mb-2">
                    <i className="fa-solid fa-globe icon-about"></i>
                    {/* <p className="fs-6 fw-bolder d-none d-none">المدينة : </p> */}
                    <p className="fs-6 me-1 ">{AboutUser ? AboutUser.city : '' } </p>
                  </li>
                  <li className="d-flex mb-2">
                    <i className="fa-solid fa-phone icon-about"></i>
                    {/* <p className="fs-6 fw-bolder d-none">رقم التليفون : </p> */}
                    <p className="fs-6 me-1">{AboutUser ? AboutUser.phone : '' } </p>
                  </li>
                  <li className="d-flex mb-2 ">
                    <i className="fa-regular fa-envelope icon-about"></i>
                    {/* <p className="fs-6 fw-bolder d-none"> */}
                      {/* البريد الألكتروني :{" "} */}
                    {/* </p> */}
                    <p className="fs-6 me-1">{AboutUser ? AboutUser.email : '' }</p>
                  </li>
                </ul>
                {/* <hr className="col-1 line-vertical" /> */}

                <ul className="col-6 mt-50 pe-5">
                  <li className="d-flex mb-2">
                    <i className="fa-solid fa-car icon-about"></i>
                    {/* <p className="fs-6 fw-bolder d-none">نوع السيارة : </p> */}
                    <p className="fs-6 me-1">{AboutUser ? AboutUser.brandCar : '' }</p>
                  </li>
                  <li className="d-flex mb-2">
                    <i className="fa-solid fa-car-side icon-about"></i>
                    {/* <p className="fs-6 fw-bolder d-none">شكل السيارة : </p> */}
                    <p className="fs-6 me-1">{AboutUser ? AboutUser.shapeCar : '' } </p>
                  </li>
                  <li className="d-flex mb-2">
                    <i className="fa-solid fa-palette icon-about"></i>
                    {/* <p className="fs-6 fw-bolder d-none">لون السيارة : </p> */}
                    <p className="fs-6 me-1">{AboutUser ? AboutUser.colorCar : '' } </p>
                  </li>
                </ul>
              </div>
            </div>
          </div> 
          {/* E N D   I N F O   D R I V E R  */}
          <hr className="line-horizontal" />

          {/* S T A R T    S U G G E S T */}  
      
<section className="row position-relative" id="suggest">
< div className="alert alert-success p-2 d-none successMessage d-flex animate__animated animate__fadeIn animate__delay-1s animate__fadeOut" role="alert">
        <i class="fa-regular fa-circle-check ms-2"></i>
        <p className="m-0">تم بنجاح</p>
      </div>
  <h2 className="mt-5">المقترحات</h2>
  <div className="col-xl-12 col-md-12">
    {allRequestsAvailable.length ? <>
      <Slider
      className="center"
      arrows={true}
      centerMode={true}
      infinite={true}
      centerPadding="100px"
      slidesToShow={1}
      speed={600}
      dots={true}
      slidesToScroll={1}
      responsive={[
        {
          
          breakpoint : 1200,
          settings : {
              slidesToShow : 1 ,
          },
        },
        {
          
            breakpoint : 1024,
            settings : {
                slidesToShow : 1 ,
                centerPadding : "40px",
            },
        },
        {
            breakpoint : 600,
            settings : {
                slidesToShow : 1,
                centerMode : true,
                centerPadding : "40px",
                arrows : false
    
            },
        },
        {
            breakpoint : 480,
            settings : {
                slidesToShow : 1,
                centerMode : true,
                centerPadding : "20px",
                arrows : false
            },
        }
      ]}>
        {allRequestsAvailable?.map((ele, i) => {
          return <>
          <div key={i} className="px-2 py-3">
            {allRequestsAvailable.length === 1 ? <>
            
              <div className=" d-flex justify-content-between align-items-center bg-MyPrimary rounded-4 location-coursol">
              <div className="text-end me-2 my-2">
                <p className=" location-1-coursol mb-4">{ele.startPoint?.split(",")[1]?.length > 20 ?".."+ele.startPoint.split(",")[1].slice(1,20): ele.startPoint.split(",")[1]}</p>
                <p className=" location-2-coursol m-0">{ele.endPoint?.split(",")[1]?.length > 20 ?".."+ele.endPoint.split(",")[1].slice(1,20): ele.endPoint.split(",")[1]}</p>
              </div>
              <div className=" ">
                <Link to={`/proposal/${ele.id}`} className="ms-2 btn-proposal fs-6">
                  تقديم عرض
                </Link>
              </div>
            </div>
            </> : <>
            <div className=" d-flex justify-content-between align-items-center bg-MyPrimary rounded-4 location-coursol">

              <div className=" ">
                <Link to={`/proposal/${ele.id}`} className="ms-2 btn-proposal fs-6">
                  تقديم عرض
                </Link>
              </div>
              <div className="text-end me-2 my-2">
                <p className=" location-1-coursol mb-4">{ele.startPoint?.split(",")[1]?.length > 20 ?ele.startPoint.split(",")[1].slice(1,20)+".." : ele.startPoint.split(",")[1]}</p>
                <p className=" location-2-coursol m-0">{ele.endPoint?.split(",")[1]?.length > 20 ?ele.endPoint.split(",")[1].slice(1,20)+".."  : ele.endPoint.split(",")[1]}</p>
              </div>
            </div>
            </>}
            
            
          </div>
          </>
        })}
      
    </Slider></> : <><div className='text-center mt-5'><p className='fs-2 '>لا يوجد هنا مقترحات</p></div></>}
    
  </div>
</section>
{/* E N D    S U G G E S T */}

{/* S T A R T    H I S T O R Y    R E Q U E S T S */}
      <section className="container" id="history-requests">
      
        <h2 className="mt-5">أخر العروض</h2>
        <HistoryRequests getToken={getToken} getRoleUser={getRoleUser} setRefresh={setRefresh}/>
      </section>
{/* E N D   H I S T O R Y    R E Q U E S T S  */}
          </> : <RingLoader className="mx-auto my-5" speedMultiplier={1} color="#cf9400" />}
        </div>
      </>
    );
}

export default AboutDriver;
//<i class="fa-solid fa-money-bill-transfer"></i> ====>>> 47n
// <i class="fa-solid fa-sack-dollar"></i>   ====>>>  s7b
