import axios from "axios";
import joi from "joi";
import { useEffect, useState } from "react";
import {useNavigate } from "react-router-dom";
import { RingLoader } from "react-spinners";
import $ from "jquery"
// import { GoogleLogin } from 'react-google-login';
// import { gapi } from 'gapi-script';

function SignIn({getValueToggle, toggleValue, decodedToken, apiUri}) {

    // const clientId = '385303598599-72qfs0t3c2a8dlcenrebkesfq33ulhd5.apps.googleusercontent.com';

    // function onSuccess(res){
        // console.log('success:', res);
    //     const user = saveUserByGoogle(res);
    //     responseSignInApi(user);
        
    // };
    // function onFailure(err) {
        // console.log('failed:', err);
    // };
    // function saveUserByGoogle(profile) {
        // console.log(user);
    //     // deep copy
    //     let myUser = {...user} 
    //     // update data
    //     myUser = {
    //         email : profile.profileObj.email,
    //         googleId : profile.profileObj.googleId,
    //         password : 'null',
    //     }
    //     // set data
        // console.log(myUser);
    //     return myUser
    //     // setUser(myUser)
    // }

    // const {decodedToken} = useContext(decodedTokenContext)
    const navigate = useNavigate()
    const [validationError, setValidationError] = useState([])
    const [serverError, setServerError] = useState([])
    const [isLoading, setIsLoading] = useState(false)
    // const [profile, setProfile] = useState(null)
    const [user, setUser] = useState({
        email : "",
        password : "",
    })


    function getUserData(e) {
  
        // console.log(user);
        // deep copy
        let myUser = {...user} 
        // update data
        myUser[e.target.name] = e.target.value
        // set data
        // console.log(myUser);

        setUser(myUser)
        // console.log(myUser);
    };
    function validationUser() {
        const schema = joi.object().required().keys({
            email : joi.string().email({tlds : false, minDomainSegments : 2}).required(),
            password :  joi.string().regex(/^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{6,}$/).required(),
        });
        return  schema.validate(user, {abortEarly : false})
    }
    async function sendData(e) {
        e.preventDefault();
        setIsLoading(true);
        const validation = validationUser();
        if (validation.error) {
            setValidationError(validation.error.details);
        }else {
            setValidationError([]);
            responseSignInApi()
            
        }
        setIsLoading(false);
    }
    async function responseSignInApi() {
        const {data} = await axios({
            url : `${apiUri}/api/v1/auth/signin/${toggleValue ? "drivers" : "riders"}`,
            method : "post",
            data : user
        })
        // console.log(data);
        if (data.message  === "welcome") {
            const token = data.token
            localStorage.setItem("token", token)
            localStorage.setItem("roleUser", toggleValue ? "drivers" : "riders")
            if (toggleValue) {
                // navigate to profile if driver 
                navigate('/about/driver');
            }else {
                // navigate to request if rider 
                navigate('/about/rider');
            }
        }else if (data.message  === "validation error"){
            setServerError([data.validationError.details])
            // console.log(data);
        }else {
            setServerError([data.message])
            // console.log(data);
        }
    }
    function changeIconPassword() {
        $(".fa-eye").hasClass("d-none") ?  $(".fa-eye").removeClass("d-none") : $(".fa-eye").addClass("d-none")
        $(".fa-eye-slash").hasClass("d-none") ? $(".fa-eye-slash").removeClass("d-none") : $(".fa-eye-slash").addClass("d-none")
        $("#password").attr("type") === "password" ? $("#password").attr("type", "text") : $("#password").attr("type", "password") ;
    }
    useEffect(() => {
        const roleUser = localStorage.getItem("roleUser")
        if (roleUser) {
            roleUser  === "riders" ? navigate("/request") : navigate("/about/driver")
        }

    //     const initClient = () => {
    //         gapi.client.init({
    //         clientId,
    //         scope: 'https://www.googleapis.com/auth/cloud-platform.read-only'
    //       });
    //    };
    //    gapi.load('client:auth2', initClient);
    }, [])
    return ( 
        <>
        <section className="container">
             {/* S T A R T   T A P S */}

             <div className="d-flex taps">
                <div>
                    <button className="bg-MySecondry taps-button-right">تسجيل الدخول</button>
                </div>
                <div >
                    <button className="" onClick={() => navigate("/signup")} >تسجيل البيانات</button>
                </div>
                
            </div>
                        {/*E N D   T A P S */}

                        {/* S T A R T   B U T T O N   T O G G L E */}
                <div className="button-cover">
                    <div className="button r" id="button-3">
                        <input type="checkbox" className="checkbox" defaultChecked={toggleValue ? true : false}   onClick={(e) => getValueToggle(e)} />
                        <div className="knobs"></div>
                        <div className="layer"></div>
                    </div>
                </div>
                    {/* E N D   B U T T O N   T O G G L E */}

            <h2 className="my-5 text-center">تسجيل الدخول كـ <span className="fw-bold">{toggleValue ? "سائق" : "راكب"}</span></h2>
             {/* <div className="d-flex justify-content-center">
             <GoogleLogin
                clientId={clientId}
                buttonText="التسجيل بواسطة جوجل"
                onSuccess={onSuccess}
                onFailure={onFailure}
                cookiePolicy={'single_host_origin'}
                isSignedIn={true}
                className="p-1 gmail-button"
            />
            </div> */}
            
            {/* <div className="d-flex justify-content-center">
                <div className="text-center m-2 d-flex justify-content-center align-items-center  gmail-button"><p className="text-MyBase px-1">التسجيل بواسطة جوجل</p><i className="fa-solid fa-g fs-3 icon-gmail"></i></div>
            </div> */}
            {/* <p className="text-center m-3 fs-4 text-muted">أو</p> */}



            <form onSubmit={sendData}>
                
                
                <div className="row justify-content-center">
                
                    <div className="input col-md-7">
                        {validationError?.map((ele, i) =>  <div key={i} className="alert alert-danger text-center 1">{ele.context.label === "password" ? "يجب ادخال كلمة السر صحيحة"  : 'البريد الألكتروني غير صحيح' }</div>)}
                        {serverError?.map((ele, i) =>  <div key={i} className="alert alert-danger text-center 2">{ele !==  "server error" ? ele : "حدث خطأ في الشبكة برجاء التواصل مع الدعم"}</div>)}       
                        <label htmlFor="email">البريد الألكتروني</label>
                        <div className="parent-icon">
                            <input type="text" className="form-control" name="email" id="email" placeholder="البريد الألكتروني" onChange={getUserData}/>
                            <i className="fa-regular fa-envelope icon"></i>
                        </div>
                    </div>

                    <div className="input col-md-7">
                        <label htmlFor="password">كلمة السر</label>
                        <div className="parent-icon">
                            <input type="password" className="form-control" name="password" id="password" placeholder="كلمة السر" onChange={getUserData}/>
                            <i className="fa-solid fa-key icon"></i>
                            <i className="fa-regular fa-eye icon-eye d-none" onClick={changeIconPassword}></i>
                            <i className="fa-regular fa-eye-slash icon-eye" onClick={changeIconPassword}></i>
                        </div>
                    </div>
                    <div className="col-12 text-center">
                        
                        {isLoading ? <RingLoader className="mx-auto" color="#cf9400" /> : <button className=" p-2 px-3 rounded-2 border-0  m-auto my-4 button-submit">تسجيل الدخول</button> }
                    </div>
                    
                </div>
            </form>
        </section>
        </>
        
        
     );
}

export default SignIn;