import axios from 'axios'
import React, { useState } from 'react'
import { useEffect } from 'react'
import { Link } from 'react-router-dom'
import Slider from 'react-slick'
import $ from 'jquery'

export default function HistoryRequests({getToken, getRoleUser, setRefresh, setRefreshRider, refreshHistory, apiUri}) {
  const role = getRoleUser()
  const [historyRequests, setHistoryRequests] = useState([])
  async function getAllHistoryRequests() {
    
    // console.log("role", role);
    const {data} = await axios({
      url : `${apiUri}/api/v1/aboneyRequests/getAboneyRequests${role === "drivers" ? "Driver" : "Rider" }?role=${getRoleUser()}`,
      method : "get",
      headers : {
        "Authorization" : `Bearer ${getToken()}`
      }
    })
    // console.log(data);
    if (data.result?.length) {
      setHistoryRequests(data.result);
      // console.log(data.result);
    }else {
      setHistoryRequests([])
    }
  }
  function confirmRemove(id) {
    $(".confirm-delete").removeClass("d-none")
    sessionStorage.setItem("id",id)
  }
  function acceptRemove(removeAccess) {
    if (removeAccess) {
      const id = sessionStorage.getItem('id')
      if (role === "drivers") {
        removeProposal(id)
      }else {
        removeRequest(id)
      }
    }else {
    }
    $(".confirm-delete").addClass("d-none")
    sessionStorage.removeItem('id');
  }
  async function removeProposal(id) {
      const body = JSON.stringify({
        "proposalId" : `${id}` 
      });
      const {data} = await axios({
          url : `${apiUri}/api/v1/aboneyProposals/removeAboneyProposal?role=drivers`,
          method : "put",
          data : body ,
          headers : {
            'Authorization' : `Bearer ${getToken()}`,
            'Content-Type': 'application/json'
          }
      })
      // console.log(data);
      if (data.message === "success") {
      getAllHistoryRequests()
      setRefresh(1)
      } 
  }
  async function removeRequest(id) {
    const body = JSON.stringify({
      "requestId" : `${id}` 
    });
    const {data} = await axios({
        url : `${apiUri}/api/v1/aboneyRequests/removeRequest?role=riders`,
        method : "put",
        data : body ,
        headers : {
          'Authorization' : `Bearer ${getToken()}`,
          'Content-Type': 'application/json'
        }
    })
    // console.log(data);
    if (data.message === "success") {
      getAllHistoryRequests()
      setRefreshRider(1)
    }else {

    }
  }
  useEffect(() => {
    // console.log("refreshHistory 1", refreshHistory);
    if (refreshHistory != 0) {
      // console.log("refreshHistory 2");
    getAllHistoryRequests();
    }
  }, [refreshHistory])
  

  useEffect(() => {
    getAllHistoryRequests()
  }, [])
  
  return ( 
    // {historyRequests ? <><div><p>لا يوجد عروض </p></div></> : <><div><p>لا يوجد عروض </p></div></>}
    
    <>
      <div className="position-relative">
        <div className="confirm-delete d-flex align-items-center d-none  animate__animated animate__fadeIn " >
          <p className='m-0'>هل انت متأكد من حذف {role === "riders" ?  "طلبك" : "عرضك"}</p>
          <button className='mx-2' onClick={() => acceptRemove(true)}>نعم</button>
          <button onClick={() => acceptRemove(false)}>لا</button>
        </div>
        {historyRequests.length ? <>
          <Slider
          className="center"
          arrows={false}
          centerMode={true}
          infinite={true}
          centerPadding="100px"
          slidesToShow={1}
          speed={600}
          dots={true}
          slidesToScroll={1}
          responsive={[
              {
                  breakpoint : 1024,
                  settings : {
                      slidesToShow : 1,
                    centerPadding : "30px",

                  },
              },
              {
                breakpoint : 800,
                settings : {
                    slidesToShow : 1,
                    centerPadding : "30px",
                },
              },
              {
                  breakpoint : 600,
                  settings : {
                      slidesToShow : 1,
                      centerMode : true,
                      centerPadding : "30px",
                      arrows : false
          
                  },
              },
              {
                  breakpoint : 480,
                  settings : {
                      slidesToShow : 1,
                      centerMode : true,
                      centerPadding : "20px",
                      arrows : false
                  },
              }
            ]}>
              {historyRequests.map((ele, i) => { 
                return <>
                
                <div key={i} className="px-2 py-3 info-card">
                
                <div className={`icon-edit-remove ${ele.acceptedDriverId || ele.isFinished ? "d-none" : ""}`}>
                    {role === "drivers" ? <>
                    <button className='remove-btn'><i className="fa-solid fa-trash mx-1" onClick={() => confirmRemove(ele.proposalId)}></i></button>
                    
                    <Link to={`/proposal/edit/${ele.proposalId}`} className='edit-btn '><i className="fa-regular fa-pen-to-square" ></i></Link>
                    </> : <>
                    <button className='edit-btn '><i className="fa-solid fa-trash" onClick={() => confirmRemove(ele.id)}></i></button>
                    </>}                  
                  </div>
                  <div className=" d-flex justify-content-between align-items-center bg-MyPrimary rounded-4 location-coursol">
                  
                    {historyRequests.length === 1 ? <>
                      <div className="text-end me-2 my-2">
                        <p className=" location-1-coursol mb-4">{ele.startPoint?.split(",")[1]?.length > 20 ? ".."+ele.startPoint.split(",")[1].slice(1,20) : ele.startPoint.split(",")[1]}</p>
                        <p className=" location-2-coursol m-0">{ele.endPoint?.split(",")[1]?.length > 20 ? ".."+ele.endPoint.split(",")[1].slice(1,20) : ele.endPoint.split(",")[1]}</p>
                      </div>
                      <ul className="d-flex justify-content-around align-items-center">
                      <li className="pt-4 px-3">
                        <p className={` text-MyBase ${ele.acceptedDriverId ? ele.isFinished ? '' : "bg-success" : "bg-danger"} rounded-pill py-1 px-2`}>
                          الحالة : <span className="p-1"> {ele.acceptedDriverId ? ele.isFinished ? "انتهي" : "متاح" : "معلق"} </span>
                        </p>
                      </li>
                      <li className="pt-4 px-3">
                        <p className=" text-MyBase">
                          السعر : <span className="p-1"> {role === "drivers" ? ele.price : typeof(ele.finalPrice) === "number" ? ele.finalPrice + " جنيه" :  "غير محدد"  } </span>
                        </p>
                      </li>
                    </ul>
                    </> : <>
                      <ul className="d-flex justify-content-around align-items-center">
                        <li className="pt-4 px-3">
                          <p className=" text-MyBase">
                            السعر : <span className="p-1"> {role === "drivers" ? ele.price : typeof(ele.finalPrice) === "number" ? ele.finalPrice + " جنيه" : "غير محدد"  } </span>
                          </p>
                        </li>
                        <li className="pt-4 px-3">
                          <p className={` text-MyBase ${ele.acceptedDriverId ? ele.isFinished ? '' : "bg-success" : "bg-danger"} rounded-pill py-1 px-2`}>
                            الحالة : <span className="p-1">  {ele.acceptedDriverId ? ele.isFinished ? "انتهي" : "متاح" : "معلق"} </span>
                          </p>
                        </li>
                      </ul>
                      <div className="text-end me-2 my-2">
                      <p className=" location-1-coursol mb-4">{ele.startPoint?.split(",")[1]?.length > 20 ?ele.startPoint.split(",")[1].slice(1,20)+".." : ele.startPoint.split(",")[1]}</p>
                        <p className=" location-2-coursol m-0">{ele.endPoint?.split(",")[1]?.length > 20 ?ele.endPoint.split(",")[1].slice(1,20)+".."  : ele.endPoint.split(",")[1]}</p>
                        </div>
                    </> }
                  </div>
                </div>
                </>

              }) }
        </Slider></> : <><div className='text-center mt-5'><p className='fs-2 '> لا يوجد هنا  {role === "drivers" ? "عروض" : "طلبات" } سابقة</p></div></>}
        
      </div>
    </>
  )
}
