import joi from "joi";
import { useEffect, useState } from "react";
import {Link, useNavigate } from "react-router-dom";
// import { tapsContext } from "../Store/TapsContext";
import $ from "jquery"
import axios from "axios";
import { RingLoader } from "react-spinners";
// import { gapi } from "gapi-script";
// import GoogleLogin from "react-google-login";

function SignUp({getValueToggle, toggleValue, apiUri}) {

    // const clientId = '385303598599-72qfs0t3c2a8dlcenrebkesfq33ulhd5.apps.googleusercontent.com';
    // const [profile, setProfile] = useState('')
    // function onSuccess(res){
        // console.log('success:', res);
    //     setProfile(res.profileObj)
    // };
    // function onFailure(err) {
    //     console.log('failed:', err);
    // };
    // function googleAuth() {
    //     const initClient = () => {
    //         gapi.client.init({
    //         clientId,
    //         scope: 'https://www.googleapis.com/auth/cloud-platform.read-only'
    //       });
    //    };
    //    gapi.load('client:auth2', initClient);
    // }
    // useEffect(() => {
    //     let myUser = {...riderUser}
    //     myUser.firstName = profile.familyName
    //     myUser.lastName = profile.givenName
    //     myUser.email = profile.email
    //     myUser.googleId = profile.googleId
    //     if (toggleValue) {
    //         setDriverUser(myUser)
    //     }else {
    //         setRiderUser(myUser)
    //     }
    // }, [profile])
    // const {location} = useContext(tapsContext)
    const navigate = useNavigate();
    const [yearsList, setYearsList] = useState([]);
    const [sizeModelCar, setSizeModelCar] = useState(1);
    const [sizeBrandCar, setSizeBrandCar] = useState(1);
    const [sizeShapeCar, setSizeShapeCar] = useState(1);
    // const [isClickColorPicker, setIsClickColorPicker] = useState(false)
    // const [colorPicker, setColorPicker] = useState(null)
    const [userNameValue, setUserNameValue] = useState("");
    const [emailValue, setEmailValue] = useState("");
    const [errorValidation, setErrorValidation] = useState([]);
    const [errorServer, setErrorServer] = useState([]);
    const [policy, setPolicy] = useState(false);
    const [isLoading, setIsLoading] = useState(false);
    const [nameLabelValidationArray, setNameLabelValidationArray] = useState([])

    const [riderUser, setRiderUser] = useState({
        firstName : "",
        lastName : "",
        userName :"",
        phone : "",
        city : "الأسكندرية",
        email : "",
        password : "",
        cPassword : "",
    });
    const [driverUser, setDriverUser] = useState({
        firstName : "",
        lastName : "",
        userName : "",
        phone : "",
        city : "الأسكندرية",
        email : "",
        password : "",
        cPassword : "",
        profilePic : "",
        idPic : "",
        brandCar : "",
        shapeCar : "",
        modelCar : "",
        colorCar : "",
    });


    // function for drop list model car
    function listYears() { 
        let currentYear = new Date().getFullYear();    
        let earliestYear = 1971;
        let yearsArr = []
        while (currentYear >= earliestYear) {        
            currentYear -= 1;    
            yearsArr.push(currentYear);     
        }
        setYearsList(yearsArr)
    };
    function getUserData(e) {
        if (toggleValue) {
            // console.log(driverUser);
            // deep copy
            let myUser = {...driverUser} 
            // update data
            myUser[e.target.name] = e.target.value
            if (e.target.name === "email") {
                myUser.userName = myUser.email.split('@')[0]
            }
            // set data
            // console.log(myUser);

            setDriverUser(myUser)
           
        }else {
            // deep copy
            let myUser = {...riderUser} 
            // update data
            myUser[e.target.name] = e.target.value
            if (e.target.name === "email") {
                myUser.userName = myUser.email.split('@')[0]
            }
            // set data
            // console.log(myUser)
            setRiderUser(myUser)
        }
    };
    function getUserNameValue(data) {
        const userName = data.email?.split('@')[0]
        setUserNameValue(userName)
        if (toggleValue) {
            let myUser = {...driverUser}
            myUser.userName = userName
            setDriverUser(myUser)

        }else {
            let myUser = {...riderUser}
            myUser.userName = userName
            setRiderUser(myUser)

        }
        
        let myUser = {...riderUser}
        myUser.userName = userName
        // console.log(myUser);
    };
    // function getEmailValue(data) {
    //     const email = data.email?.split('@')[0]
    //     setEmailValue(email)
        
    //     let myUser = {...riderUser}
    //     myUser.email = email
    //     setDriverUser(myUser)
    // };
    function validationUserData(type, user) {
        const schema = {
            riderSchema : joi.object().required().keys({
                firstName : joi.string().min(3).max(10).required(),
                lastName : joi.string().min(3).max(10).required(),
                userName : joi.string().lowercase().min(3).max(15).required(),
                email : joi.string().email({tlds : false, minDomainSegments : 2}),
                password :  joi.string().regex(/^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{6,}$/).required(),
                cPassword :  joi.valid(joi.ref('password')).required(),
                city : joi.string().min(3).required(),
                phone : joi.string().alphanum().length(11).required(),
                googleId :joi.string().regex(/(\d+|null)/),
            }),
            driverSchema : joi.object().required().keys({
                firstName : joi.string().min(3).max(10).required(),
                lastName : joi.string().min(3).max(10).required(),
                userName : joi.string().lowercase().min(3).max(15).required(),
                email : joi.string().email({tlds : false, minDomainSegments : 2}).required(),
                password :  joi.string().regex(/^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{6,}$/).required(),
                cPassword :  joi.valid(joi.ref('password')).required(),
                city : joi.string().min(3).required(),
                googleId :joi.string().regex(/(\d+|null)/),
                phone : joi.string().alphanum().length(11).required(),
                profilePic : joi.string(),
                idPic : joi.string(),
                brandCar : joi.string().required(),
                modelCar : joi.string().required(),
                shapeCar : joi.string().required(),
                colorCar : joi.string().required(),
                })
        }
        return schema[type].validate(user, {abortEarly : false});
        
    };
    function validationPassword(e) {
        const passwordSchema = /^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{6,}$/;

        // const valid = schema.validate(toggleValue ? driverUser : riderUser, {abortEarly : false});
        if (passwordSchema.test(toggleValue ? driverUser.password : riderUser.password)) {
            // console.log(passwordSchema.test(toggleValue ? driverUser.password : riderUser.password));
            $(`.medium-pass p`).text("قوي").removeClass("text-danger")
            $(".medium-pass").removeClass("d-none");
        }else {
            $(`.medium-pass p`).text("ضعيف").addClass("text-danger")
            $(".medium-pass").removeClass("d-none");

        }        
    };
    function validationConfirmPassword() {
        if (toggleValue ? driverUser.password === driverUser.cPassword : riderUser.password === riderUser.cPassword) {
            // console.log(toggleValue ? driverUser.password === driverUser.cPassword : riderUser.password === riderUser.cPassword);
            $(".c-pass").removeClass("d-none")
            $(".c-pass p").text("متطابق").removeClass("text-danger")
        }else
        {
            // console.log(toggleValue ? driverUser.password === driverUser.cPassword : riderUser.password === riderUser.cPassword);
            $(".c-pass p").text("غير متطابق").addClass("text-danger")
            $(".c-pass").removeClass("d-none")
        }
    };

    
    // ** FUNCTION TO CALL API AND SEND DATA
    async function sendData(e) {
        e.preventDefault();
        setIsLoading(true)
        if (!policy) {
            setErrorValidation([])
            // console.log("policy" ,policy);
            setIsLoading(false)
            setErrorServer(['you must accept policy user to contain'])
            return 0;
        }
        
        // toggleValue ?   getUserNameValue(driverUser) : getUserNameValue(riderUser);
        const validationError = toggleValue ?  validationUserData("driverSchema", driverUser) : validationUserData("riderSchema", riderUser);
        if (validationError?.error) {
            setErrorServer([])
            // console.log(validationError.error.details);
            setErrorValidation(["يرجي أدخال جميع البيانات بشكل صحيح"])
            handleErrorValidation(validationError.error.details)
            // console.log(validationError.error.details);
            // setIsLoading(false)
        }else {
            handleErrorValidation()
            setErrorServer([])
            setErrorValidation([])

            //Api Call
                    // ** axios way
            const {data} = await axios({
                url : `${apiUri}/api/v1/auth/signup/${toggleValue ? "drivers" : "riders"}`,
                method : "post",
                data : toggleValue ? driverUser : riderUser,
                headers : {
                    'Access-Control-Allow-Origin' : "*",
                    "Accept" : "application/json",
                    "Content-Type" : "application/json",
                } ,
            })
            // console.log(data);
            if (data.message  === "success") {
                const token = data.token
                localStorage.setItem("token", token)
                localStorage.setItem("roleUser", toggleValue ? "drivers" : "riders")
                if (toggleValue) {
                    // navigate to profile if driver 
                    navigate('/about/driver');
                }else {
                    // navigate to request if rider 
                    navigate('/about/rider');

                }

            }else if (data.message  === "validation error"){
                setErrorValidation(["يرجي أدخال جميع البيانات بشكل صحيح"])
            }else {
                setErrorValidation([])
                setErrorServer([data.message])
            }

                // ** fetch way
            // const response = await fetch('${apiUri}/api/v1/auth/signup/riders', {
            //     method: 'POST',
            //     headers: {
            //     'Content-Type': 'application/json; charset=UTF-8'
            //     },
            //     body: JSON.stringify(toggleValue ? driverUser : riderUser)
            // });
            // const data2 = await response.json()
            // console.log(data2);
        }
        setIsLoading(false)

    }
    function handleErrorValidation(errors = []) {
        // console.log('2',nameLabelValidationArray);
        let myNameLabelValidationArray = [] 
        nameLabelValidationArray.forEach(ele => {
            if ($(`#${ele}`).hasClass("form-control-border-error") ) {
                $(`#${ele}`).removeClass("form-control-border-error")
                // console.log("why");
            }
        });
        errors.forEach(ele => {
        myNameLabelValidationArray.push(ele.context.label)
            if (!$(`#${ele.context.label}`).hasClass("form-control-border-error") ) {
                $(`#${ele.context.label}`).addClass("form-control-border-error")
            }
       });
    //    console.log(myNameLabelValidationArray);
       setNameLabelValidationArray(myNameLabelValidationArray)
    }

    function displayFilePath(fileName) {
        const filePath = driverUser[fileName].split(':\\fakepath\\')[1]
        if (filePath.length > 36) {
            return "..." + filePath.slice(0,36)
        }else
        {
            return filePath
        }
    }
    useEffect(() => {
        // localStorage.clear()
        listYears()
    }, [])

    useEffect(() => {
        if (riderUser.email !== "" || driverUser.email !== "" ) {
            // console.log("hhaaa");
            getUserNameValue(toggleValue ? driverUser : riderUser)
        }
    }, [riderUser.email, driverUser.email])

    useEffect(() => {
        if (riderUser.cPassword !== "" || riderUser.password !== "" || driverUser.cPassword !== "" || driverUser.password !== "" ) {
            validationConfirmPassword()
        } 
    }, [riderUser.cPassword, driverUser.cPassword,riderUser.password, driverUser.password ])

    function changeIconPassword() {
        $(".fa-eye").hasClass("d-none") ?  $(".fa-eye").removeClass("d-none") : $(".fa-eye").addClass("d-none")
        $(".fa-eye-slash").hasClass("d-none") ? $(".fa-eye-slash").removeClass("d-none") : $(".fa-eye-slash").addClass("d-none")
        $("#cPassword").attr("type") === "password" ? $("#cPassword").attr("type", "text") : $("#cPassword").attr("type", "password") ;
        $("#password").attr("type") === "password" ? $("#password").attr("type", "text") : $("#password").attr("type", "password") ;
    }
    return ( 
        <>
                        {/* S T A R T   R I D E R S  &&  D R I V E R S */}
        <section className="container"> 
                        {/* S T A R T   T A P S */}

            <div className="d-flex taps">
                <div>
                    <button className="" onClick={() => navigate("/signin")}>تسجيل الدخول</button>
                </div>
                <div >
                    <button className="bg-MySecondry taps-button-left" >تسجيل البيانات</button>
                </div>
                
            </div>
                        {/*E N D   T A P S */}

            {/* S T A R T   B U T T O N   T O G G L E */}
                <div className="button-cover">
                    <div className="button r" id="button-3">
                        <input type="checkbox" defaultChecked={toggleValue ? true : false} onClick={(e) => getValueToggle(e)} className="checkbox" />
                        <div className="knobs"></div>
                        <div className="layer"></div>
                    </div>
                </div>
            {/* E N D   B U T T O N   T O G G L E */}


        <h2 className=" my-5 text-center">أكمل ملفك الشخصي كـ <span className="fw-bold">{toggleValue? "سائق" : "راكب"}</span></h2>
        
        {/* <div className="d-flex justify-content-center">
                <div className="text-center m-2 d-flex justify-content-center align-items-center  gmail-button"><p className="text-MyBase px-1">التسجيل بواسطة جوجل</p><i className="fa-solid fa-g fs-3 icon-gmail"></i></div>
            </div> */}
            {/* <div className="d-flex justify-content-center" onClick={googleAuth}>
             <GoogleLogin
                clientId={clientId}
                buttonText="التسجيل بواسطة جوجل"
                onSuccess={onSuccess}
                onFailure={onFailure}
                cookiePolicy={'single_host_origin'}
                isSignedIn={true}
                className="p-1 gmail-button"
            />
            </div>
            <hr  className=" w-25 m-auto my-5 line-dotted"/> */}

        <form onSubmit={sendData} >
            <div className="container">
                    {/* {errorValidation?.map((ele, i) => <div key={i} className="alert alert-danger text-center">{ele.context.label === "password" || ele.context.label === "cPassword" ? "Minimum 6 characters, at least one letter and one number"  : "يرجي أدخال جميع البيانات بشكل صحيح"}</div>)} */}
                    {errorValidation?.map((ele, i) => <div key={i} className="alert alert-danger text-center">{ele}</div>)}
                    {errorServer?.map((ele, i) => <div key={i} className="alert alert-danger text-center">{ele !==  "server error" ? ele : "حدث خطأ في الشبكة برجاء التواصل مع الدعم"}</div>)}            

                <div className="row">
                    <div className="input col-md-6">
                    
                        <label htmlFor="firstName">الأسم الأول</label>
                        <div className="parent-icon">
                        <input type="text" className="form-control" name="firstName" id="firstName" placeholder="الأسم الأول" onChange={getUserData} />
                        <i className="fa-regular fa-user icon"></i>
                        </div>
                    </div>
                    <div className="input col-md-6">
                        <label htmlFor="last-name">الأسم الأخير</label>
                        <div className="parent-icon">
                            <input type="text" className="form-control" name="lastName" id="lastName" placeholder="الأسم الأخير" onChange={getUserData} />
                            <i className="fa-regular fa-user icon"></i>
                        </div>
                    </div>
                    <div className="input col-md-6">
                        <label htmlFor="phone">رقم التليفون</label>
                        <div className="parent-icon">
                            <input type="tel" className="form-control" name="phone" id="phone" placeholder="رقم التليفون" onChange={getUserData} />
                            <i className="fa-solid fa-phone icon"></i>
                        </div>
                    </div>
                    <div className="input col-md-6">
                        <label htmlFor="city">المدينة</label>
                        <div className="parent-icon">
                            <select name="city" id="city" defaultValue="الأسكندرية" className="w-100 form-select"  onChange={getUserData}>
                                <option  disabled>أختار المدينة</option>
                                <option value="الأسكندرية"  >الأسكندرية</option>
                            </select>
                        </div>
                    </div>
                    
                    <hr  className=" w-50 m-auto my-5"/>

                    <div className="input col-md-12">
                        <label htmlFor="email">البريد الألكتروني</label>
                        <div className="parent-icon">
                            <input type="text" className="form-control" name="email" id="email" placeholder="البريد الألكتروني" onChange={getUserData} />
                            <i className="fa-regular fa-envelope icon"></i>
                        </div>
                    </div>
                    <div className="input col-md-12">
                        <label htmlFor="userName">اسم العميل</label>
                        <div className="parent-icon">
                            <input type="text" className="form-control" name="userName" id="userName" placeholder="اسم العميل"   value={userNameValue} onChange={(e) => {setUserNameValue(e.target.value); getUserData(e);}}/>
                            <i className="fa-regular fa-user icon"></i>
                        </div>
                    </div>

                    <div className="input col-md-6">
                        <label htmlFor="password">كلمة السر</label>
                        <div className="parent-icon">
                            <input type="password" className="form-control" name="password" id="password" placeholder="كلمة السر" onChange={(e) => {getUserData(e); validationPassword(e)} } />
                            <i className="fa-solid fa-key icon"></i>
                            <i className="fa-regular fa-eye icon-eye d-none" onClick={changeIconPassword}></i>
                            <i className="fa-regular fa-eye-slash icon-eye" onClick={changeIconPassword}></i>
                        </div>

                        <div className="medium-pass d-none">
                            <p className="d-inline-block text-center">ضعيف</p>
                        </div>

                    </div>
                    <div className="input col-md-6">
                        <label htmlFor="cPassword">تأكيد كلمة السر</label>
                        <div className="parent-icon">
                            <input type="password" className="form-control" name="cPassword" id="cPassword" placeholder="تأكيد كلمة السر" onChange={(e) => {getUserData(e);} } />
                            <i className="fa-solid fa-key icon"></i>
                            <i className="fa-regular fa-eye icon-eye d-none" onClick={changeIconPassword}></i>
                            <i className="fa-regular fa-eye-slash icon-eye " onClick={changeIconPassword}></i>
                        </div>
                        <div className="c-pass d-none">
                            <p className="d-inline-block text-center ">متطابق</p>
                        </div>
                    </div>
                            {/* E N D   R I D E R S */}
                    {toggleValue ? <>
                        <div className="col-md-12">
                    <hr  className=" w-50 m-auto my-5"/>
                    </div>
                    

                    <div className="input col-md-6">
                        <label htmlFor="pic-driver">صورة السائق</label>
                        <div className="">
                            {/* <input type="file" accept="	image/png,image/jpeg" className="form-control" name="pic-driver" id="pic-driver" placeholder="صورة السائق" /> */}
                            <input type="file" name="profilePic" id="profilePic" className="inputfile" onChange={(e) => getUserData(e)}  />
                            <label className="my-2" htmlFor="profilePic">{driverUser.profilePic ? displayFilePath('profilePic')  : <> <i className="fa-solid fa-download icon-file"></i> أختار صورة ...</>} </label>
                        </div>
                    </div>
                    <div className="input col-md-6">
                        <label htmlFor="pic-id-driver">بطاقة رخصة السائق</label>
                        <div className="">
                            {/* <input type="file" accept="	image/png,image/jpeg" placeholder="صورة السائق"  className="form-control" name="pic-id-driver" id="pic-id-driver" /> */}
                            <input type="file" name="idPic" id="idPic" className="inputfile" onChange={(e) => getUserData(e)}  />
                            {/* <i className="fa-solid fa-download icon-file"></i> */}
                            <label className="my-2" htmlFor="idPic">{driverUser.idPic ? displayFilePath("idPic")  : <> <i className="fa-solid fa-download icon-file"></i> أختار صورة ...</>} </label>
                            
                        </div>
                    </div>
                    <div className="select col-md-3">
                        <label htmlFor="brandCar">نوع السيارة</label>
                        <select name="brandCar" className="w-100 form-select" id="brandCar" defaultValue={"default"}  size={sizeBrandCar} onFocus={()=>{setSizeBrandCar(5)}} onBlur={()=>{setSizeBrandCar(1)}} onChange={(e)=>{e.target.blur(); getUserData(e);}}>
                            <option value="default" disabled>أختار نوع سيارتك</option>
                            <option value="Alfa Romeo">Alfa Romeo - الفا روميو</option>
                            <option value="Audi">Audi - اودي</option>
                            <option value="BMW">BMW - بي ام دبليو</option>
                            <option value="Chevrolet">Chevrolet - شيفروليت</option>
                            <option value="Daewoo">Daewoo - دايو</option>
                            <option value="Daihatsu">Daihatsu - داي هاتسو</option>
                            <option value="FIAT">FIAT - فيات</option>
                            <option value="Ford">Ford - فورد</option>
                            <option value="Honda">Honda - هوندا</option>
                            <option value="HUMMER">HUMMER - هامر</option>
                            <option value="Hyundai">Hyundai - هونداي</option>
                            <option value="Jeep">Jeep- جيب</option>
                            <option value="Kia">Kia - كيا</option>
                            <option value="Maserati">Maserati - مازيراتي</option>
                            <option value="MAZDA">MAZDA - مازدا</option>
                            <option value="Mercedes-Benz">Mercedes-Benz - مرسيدس بينز</option>
                            <option value="MINI">MINI - ميني كوبر</option>
                            <option value="Mitsubishi">Mitsubishi - ميتسوبيشي</option>
                            <option value="Nissan">Nissan - نيسان</option>
                            <option value="Subaru">Subaru - سبارو</option>
                            <option value="Suzuki">Suzuki - سزوكي</option>
                            <option value="Toyota">Toyota - تويوتا</option>
                            <option value="Volkswagen">Volkswagen - فولك سويجان</option>
                            <option value="Volvo">Volvo - فولفو </option>
                        </select>
                    </div>
                    <div className="select col-md-3">
                        <label htmlFor="shapeCar">شكل السيارة</label>
                        <select name="shapeCar" id="shapeCar" className="w-100 form-select" defaultValue={"default"} size={sizeShapeCar} onFocus={()=>{setSizeShapeCar(4)}} onBlur={()=>{setSizeShapeCar(1)}} onChange={(e)=>{e.target.blur(); getUserData(e);}}>
                            <option value="default" disabled>أختار شكل سيارتك</option>
                            <option value="SUV">SUV</option>
                            <option value="سيدان">سيدان</option>
                            <option value="كوبيه">كوبيه</option>
                            <option value="هاتش باك">هاتش باك</option>
                        </select>
                    </div>
                    <div className="select col-md-3">
                            {/* https://betterprogramming.pub/how-to-dynamically-populate-a-year-dropdown-with-javascript-bcf4f849bc4f */}
                            <label htmlFor="modelCar">موديل السيارة</label>
                            <select name="modelCar" className="w-100 form-select" id="modelCar" defaultValue={"default"}  size={sizeModelCar} onFocus={()=>{setSizeModelCar(5)}} onBlur={()=>{setSizeModelCar(1)}} onChange={(e)=>{e.target.blur(); getUserData(e); }}>
                                <option value="default" disabled>أختار موديل سيارتك</option>
                                {yearsList.map((ele, i) => <option key={i} value={`${ele}`}>{ele}</option>)}
                            </select>
                        </div>
                    <div className="input col-md-3" >
                            <label htmlFor="colorCar" >لون السيارة</label>
                            <div className="parent-icon" > 
                                 <input type="text" className="form-control" name="colorCar" id="colorCar" placeholder="لون السيارة كما في الرخصة" onChange={getUserData}  />
                                <i className="fa-solid fa-palette icon"></i> 
                            </div> 
                            

                    </div></> : ""}

                </div>

                            {/* E N D   D R I V E R S  */}


                <div className="my-4">
                    <input type="checkbox" id="policy" name="policy" className="" onChange={(e) => setPolicy(e.target.checked)} />
                    <label htmlFor="policy" className="mx-2">الموافقة علي شروط الأستخدام <Link className="text-warning" to={"/helpCenter/privacyPolicy"}>Privacy Policy</Link> و <Link className="text-warning" to={"/helpCenter/termsService"}>Terms Services</Link></label>
                </div>
                <div className="col-md-12 text-center mb-5">
                     {isLoading ? <RingLoader className="mx-auto" color="#cf9400" /> : <button type="submit" className=" p-2 px-3 rounded-2 border-0  m-auto my-4 button-submit">تسجيل البيانات</button> }

                </div>
            </div> 
        </form>
        </section>

        {/* <div className="container">
        <input type="file" name="file" id="file" className="inputfile" onChange={(e) => setFileName(e.target.value)}  />
        <label htmlFor="file">{fileName ? fileName.split(':\\fakepath\\')[1].length > 40 ? "..." + fileName.split(':\\fakepath\\')[1].slice(0,40)  : fileName.split(':\\fakepath\\')[1].slice(0,40) : 'Choose a file'} </label>
        </div> */}
        <div>
        {/* <SketchPicker onChangeComplete={getColorPicker} />  */}
        </div>
        </>
        
     );
}

export default SignUp;