import './App.css';
import {Navigate, Route, Routes, useLocation, useNavigate, } from "react-router-dom";
import SignIn from './components/SignIn/SignIn';
import SignUp from './components/SignUp/SignUp';
import Intro from './components/Intro/Intro';
import AboutRider from './components/AboutRider/AboutRider';
import AboutDriver from './components/AboutDriver/AboutDrivers';
import AboneyRequest from './components/AboneyRequest/AboneyRequest';
import AboneyProposals from './components/AboneyProposal/AboneyProposals';
import NotFound from './components/NotFound/NotFound';
import { useEffect, useState } from 'react';
import decode from 'jwt-decode'
import ApiAboutUserProvider from './components/Store/ApiAboutUserContext';
import moment from 'moment';
import Wallet from './components/Wallet/Wallet';
import PrivacyPolicy from './components/PrivacyPolicy/PrivacyPolicy';
import TermsService from './components/TermsService/TermsService';
const apiKeyGoogleMaps ='AIzaSyA-U_utl-_ITMODJx79MoxzoLewye4woqI'
function App() {
console.warn = () => {}

  // const apiUri = "https://aboney.com/backend" // deploy
  // const apiUri = "http://localhost:3000"     // dev 1
  const apiUri = "https://abon-test-4vn4-oye9uyw41-anglofawzy.vercel.app"     // dev 2

  const locate = useLocation()
  const location = locate.pathname
  const [toggleValue, setToggleValue] = useState(false)
  const navigate = useNavigate()      

  function loader() {
    if (location === '/') {
      // console.log(location);
      setTimeout( () => {
        if (localStorage.getItem("token")) {
          // console.log("kfaia");
          const token = localStorage.getItem("token")
          const decoded = decode(token)
          // console.log(decoded);
          decoded.role === "riders" ? navigate('/about/rider') :  navigate('/about/driver')
        }else {
          navigate('/signin')
        }
        
    }, 4000);      
    }
  }
  function getValueToggle(e) {
    setToggleValue(e.target.checked);
    // console.log(e.target.checked);
  }
  function ProtectedRoute(props) { // shield for routes
    if (!localStorage.getItem("token")) {
      //novigate to login
      // if (location === "/signin") {
      //   return 0;
      // }
      return <Navigate to='/signin' />
    }else
    {
      // console.log("yes");
      return props.children;
    }
  }
  function ProtectedDriverRoute(props) { // shield for routes
      if (localStorage.getItem("roleUser") === "drivers") {
        return  props.children;
      } else {
        return <Navigate to='/notFound' />
      }
  }
  function ProtectedRiderRoute(props) { // shield for routes
    if (localStorage.getItem("roleUser") === "riders") {
      return  props.children;
    } else {
      return <Navigate to='/notFound' />
    }
  }
  function getToken() {
    let token = localStorage.getItem("token")
    return token
  }
  function getRoleUser() {
    let roleUser = localStorage.getItem("roleUser")
    return roleUser
  }
  function getDifferenceDays(a, b) {
        let start = moment(a);
        let end = moment(b);
        let differenceDays = end.diff(start, 'days');
        // console.log( "differenceDays",differenceDays);
        return differenceDays
  }
  function coreUnitPrice(days) {
    // let days = getDifferenceDays(a, b)
    if (days < 28) {
        return("المدة")
    }else if(days >= 28) {
        let weeks = Math.ceil( days / 7)
        if (weeks < 8) {
            return("أسبوعين")
        }else if (weeks >= 8) {
            return("شهر")
        }
    }
}
  useEffect(()=> {
    loader()
  }, [])

  return (
  <>
  {/* {decodedToken ? } */}
  <ApiAboutUserProvider getToken={getToken} getRoleUser={getRoleUser} apiUri={apiUri}>
    <Routes>
        <Route element={<Intro/>} path="/"/>
        <Route element={<SignIn getValueToggle={getValueToggle} toggleValue={toggleValue} apiUri={apiUri}  />} path="signin"/>
        <Route element={<SignUp getValueToggle={getValueToggle} toggleValue={toggleValue} apiUri={apiUri} />} path="signup"/>
        <Route element={<ProtectedRoute><ProtectedRiderRoute><AboutRider apiUri={apiUri} getToken={getToken} getRoleUser={getRoleUser} getDifferenceDays={getDifferenceDays} coreUnitPrice={coreUnitPrice} /></ProtectedRiderRoute></ProtectedRoute>} path="about/rider"/>
        <Route element={<ProtectedRoute><ProtectedDriverRoute><AboutDriver apiKeyGoogleMaps={apiKeyGoogleMaps} getToken={getToken} getRoleUser={getRoleUser}/></ProtectedDriverRoute></ProtectedRoute>} path="about/driver"/>
        <Route element={<ProtectedRoute><ProtectedRiderRoute><AboneyRequest apiUri={apiUri} getToken = {getToken} getDifferenceDays={getDifferenceDays} /></ProtectedRiderRoute></ProtectedRoute>} path="request"/>
        <Route element={<ProtectedRoute><ProtectedDriverRoute><AboneyProposals apiUri={apiUri} getToken={getToken} getDifferenceDays={getDifferenceDays} coreUnitPrice={coreUnitPrice} /></ProtectedDriverRoute></ProtectedRoute>} path="proposal/:id"/>
        <Route element={<ProtectedRoute><ProtectedDriverRoute><AboneyProposals apiUri={apiUri} getToken={getToken} getDifferenceDays={getDifferenceDays} coreUnitPrice={coreUnitPrice} /></ProtectedDriverRoute></ProtectedRoute>} path="proposal/edit/:proposalId"/>
        <Route element={<ProtectedRoute><ProtectedRiderRoute><Wallet apiUri={apiUri} getToken={getToken} getRoleUser={getRoleUser}/></ProtectedRiderRoute></ProtectedRoute>} path="wallet/deposit"/>
        <Route element={<ProtectedRoute><ProtectedDriverRoute><Wallet apiUri={apiUri} getToken={getToken} getRoleUser={getRoleUser}/></ProtectedDriverRoute></ProtectedRoute>} path="wallet/withdrawal"/>
        <Route element={<TermsService/>} path="helpCenter/termsService"/>
        <Route element={<PrivacyPolicy/>} path="helpCenter/privacyPolicy"/>
        
        
        <Route element={<NotFound/>} path="*"/>
    </Routes>  
    </ApiAboutUserProvider>  
  </>
  );
}

export default App;