import React, { useState, useEffect } from 'react'
import {  GoogleMap, LoadScript, Marker, Autocomplete } from '@react-google-maps/api';

const containerStyle = {
  width: '100%',
  height: '550px'
};


function TestMap( {onMarkerDragEnd, onPlaceSelected, center, zoom, apiKeyGoogleMaps, type} ) {
  // type = "marker"
  let typeServices = type
  
  const centerPosition = {
    lat: center.lat ,
      lng: center.lng
  };
  // const position = {
  //   lat: center.lat ,
  //   lng: center.lng
  // }

  const [autoComplete, setAutocomplete] = useState(null)
  const [markDrag, setMarkDrag] = useState(null)
  const [map, setMap] = useState(null);
  // const [centerPosition, setCenterPosition] = useState(centerPos);
  const [positionMarker, setPositionMarker] = useState(centerPosition);


  let refMap = null;

  const onLoad = React.useCallback(function callback(map) {
    // This is just an example of getting and using the map instance!!! don't just blindly copy!
    const bounds = new window.google.maps.LatLngBounds(centerPosition);
    map.fitBounds(bounds);

    setMap(map)
  }, [])
        // M A R K E R 
   const loadMarker = marker => {
    //  console.log('marker: ', marker);
     setMarkDrag(marker)
     
  }
   
  
  useEffect(() => {
    type = "marker"
    typeServices = 'marker'
  },[])
  // console.log("center", center, type);


  // A U T O  C O M P L E T E
  
   function loadAutocomplete (autocomplete) {
    //  console.log('autocomplete: ', autocomplete)

    setAutocomplete(autocomplete)
  }

   function onPlaceChanged () {
    if (autoComplete !== null) {
       onPlaceSelected(autoComplete.getPlace())
    } else {
      // console.log('Autocomplete is not loaded yet!')
    }
  }



  const onUnmount = React.useCallback(function callback(map) {
    setMap(null)
  }, [])

  // let x = 2;

  function handleCenter() {
    type= "marker"
    if (refMap?.state.map) {
      const mapCenter = refMap.state.map.center
      const newLat = mapCenter.lat()
      const newLng = mapCenter.lng()
      // console.log('googleMap2', refMap, mapCenter);
      // console.log("mapCenter", newLat, newLng);
      const center = { 
        lat :newLat,
        lng : newLng
      }
      setPositionMarker(center)
      onMarkerDragEnd(center)
    }
  }
  function mapPosition() {
    if (typeServices === "current") {
      return centerPosition
    }else if (typeServices === "autocompelete") {
      return centerPosition
    }
  }
  

  return  (
    <>
    <div className='position-relative d-block'>
    <LoadScript
    googleMapsApiKey={apiKeyGoogleMaps}
    libraries={[`places`, "drawing", "geometry", "localContext", "visualization"]}
    loadingElement = {Marker}
    >
      <GoogleMap
        id='aboney-map'
        ref={(ref) => {refMap = ref}}
        mapContainerStyle={containerStyle}
        // {type === "current" ? center={centerPosition} : "" }
        // {type === "autocompelete" ? center={centerPosition} : <></> }
        center={mapPosition()}
        onLoad={onLoad}
        onUnmount={onUnmount}
        zoom = {12}
        onCenterChanged={() => handleCenter()}
      >
        { /* Child components, such as markers, info windows, etc. */ }
        <>
        {/* <Marker position={positionMarker}/> */}
        
        <Marker
          onLoad={loadMarker}
          position={type == "marker" ? positionMarker : centerPosition }
        />    
        <Autocomplete
            onLoad={loadAutocomplete}
            onPlaceChanged={onPlaceChanged}
          >
            <input
              type="text"
              placeholder="البحث..."
              style={{
                boxSizing: `border-box`,
                border: `1px solid transparent`,
                width: `240px`,
                height: `32px`,
                padding: `0 12px`,
                borderRadius: `3px`,
                boxShadow: `0 2px 6px rgba(0, 0, 0, 0.3)`,
                fontSize: `14px`,
                outline: `none`,
                textOverflow: `ellipses`,
                position: "absolute",
                left: "50%",
                marginLeft: "-120px"
              }}
            />
          </Autocomplete>
       </>
      </GoogleMap>
      </LoadScript>
      

    </div>
    
    </>


  )
}

export default TestMap;






