import { Link } from 'react-router-dom'

export default function RequestAboneyBtn() {

  function reloadRequestPage() {
    sessionStorage.setItem('isReload', "true");
  }

  return ( 
  <>
    <div className='text-start my-5 mx-1  m-sm-5 '>
        <Link to={'/request'} className='request-btn' onClick={reloadRequestPage}>
            <i className="fa-solid fa-plus"></i>
        </Link>
    </div>
  </>
    
  )
}