
import axios from "axios";
import joi from "joi";
import { useState } from "react";
import {  useNavigate } from "react-router-dom";

import React from 'react'
import AboutUserBtn from "../AboutUserBtn/AboutUserBtn";

export default function Wallet({getToken, getRoleUser, apiUri}) {
    const navigate = useNavigate()
    const role = getRoleUser()
    const [code, setCode] = useState("002")
    const [validationError, setValidationError] = useState([])
    const [data, setData] = useState({
        phone : "",
        amount : "",
        service : ""
    })

    function getData(e) {
  
        // console.log(data);
        // deep copy
        let myUser = {...data} 
        // update data
        if (e.target.name === "phone") {
            myUser[e.target.name] = code + e.target.value
        }else {
            myUser[e.target.name] = e.target.value
        }
        // myUser[e.target.name] = e.target.value
        // set data
        // console.log(myUser);

        setData(myUser)
        // console.log(myUser);
    };
    function validationData() {
        const schema = joi.object().required().keys({
            phone : joi.string().min(11).required(),
            amount :  joi.string().min(1).required(),
            service : joi.string().required()
        });
        return  schema.validate(data, {abortEarly : false})
    }
    async function sendData(e) {
        e.preventDefault();
        const validation = validationData();
        if (validation.error) {
            setValidationError(["يرجي أدخال البيانات بطريقة صحيحة"]);
            // console.log(validation.error);
        }else {
            setValidationError([]);
            // console.log(role);
            const response = await axios({
                url : `${apiUri}/api/v1/paymentTransfer/${role === "riders" ? "deposit" : "withdrawal" }?role=${role}`,
                method : "post",
                data : data,
                headers : {
                    'Authorization': `Bearer ${getToken()}`
                }
            })
            // console.log(response);
            if (response.data.message  === "success") {
                if (role === "drivers") {
                    // navigate to profile if driver 
                    navigate('/about/driver');
                }else {
                    // navigate to request if rider 
                    navigate('/about/rider');
                }
                sessionStorage.setItem("successMessage", "true")
            }else {
                // console.log(response);
            }
        }
    }
  return <>
    <form onSubmit={sendData}>
    
    {/* {serverError.map((ele, i) =>  <div key={i} className="alert alert-danger text-center">{ele}</div>)} */}
    <div className="container">
    <AboutUserBtn/>
        <div className="row flex-column justify-content-center align-items-center my-5">
        {validationError.map((ele, i) =>  <div key={i} className="alert alert-danger text-center">{ele}</div>)}
            <h2 className="text-center">{role === "riders" ? "أيداع" : "سحب"}</h2>
            <div className="input col-md-6">
                <p className="alert alert-warning"><i class="fa-solid fa-triangle-exclamation"></i> سيتم مراجعة الطلبات فور استلامها... </p>
                <label htmlFor="phone">رقم التليفون الخاص بالمحفظة</label>
                <div className="d-flex">
                <div className=" w-100"  dir="ltr">
                    <input type="tel" className="form-control w-100"  name="phone" id="phone"  placeholder="رقم التليفون الخاص بالمحفظة" onChange={getData}/>                
                </div>
                <div className="code parent-icon" >
                <i className="fa-solid fa-phone icon-code "></i>
                    
                    <input dir="ltr" type="tel" maxLength={5} className="form-control code-phone" value={code} onChange={(e) => setCode(e.target.value)} name="email" id="email" placeholder="كود"/>
                </div>
                
                </div>
                
            </div>

            <div className="input col-md-6">
                <label htmlFor="amount">المبلغ</label>
                <div className="parent-icon">
                    <input type="amount" className="form-control" name="amount" id="amount" placeholder="المبلغ" onChange={getData}/>
                    <i className="fa-regular fa-money-bill-1 icon"></i>
                </div>
            </div>
            <div className="input col-md-6">
                <label htmlFor="service">خدمة التحويل</label>
                <div className="parent-icon">
                    <select type="service" className="form-select" name="service" id="service" placeholder="خدمتك" onChange={getData}>
                        <option disabled selected value="">اختر خدمتك</option>
                        <option value="orange cash">orange cash</option>
                        <option value="vodafone cash">vodafone cash</option>
                    </select>
                </div>
            </div>
            <button className="deposit-btn">إرسال التفاصيل</button>
            <div className="col-12 text-center">
                
                {/* {isLoading ? <RingLoader className="mx-auto" color="#cf9400" /> : <button className=" p-2 px-3 rounded-2 border-0  m-auto my-4 button-submit">تسجيل الدخول</button> } */}
            </div>
            
        </div>
    </div>
    
</form>
</>
}

