import React from 'react'
import Geocode from "react-geocode";
import TestMap from '../TestMap/TestMap';
const apiKeyGoogleMaps = "AIzaSyA-U_utl-_ITMODJx79MoxzoLewye4woqI"

// console.warn = () => {}

Geocode.setApiKey(apiKeyGoogleMaps);
Geocode.enableDebug();



class Map extends React.Component{
constructor( props ){
  

  super( props );
  this.state = {
   address: '',
   city: '',
   area: '',
   state: '',
   mapPosition: {
    lat: this.props.center.lat,
    lng: this.props.center.lng
   },
   markerPosition: {
    lat: this.props.center.lat,
    lng: this.props.center.lng
    },
    newCurrentPosition : {
      lat : this.props.center.lat,
      lng : this.props.center.lng
    },
    type : ""
  }
 }
 
/**
  * Get the current address from the default map position and set those values in the state
  */
 componentDidMount() {
  // console.log("map", this.props.center);
  Geocode.fromLatLng( this.state.mapPosition.lat , this.state.mapPosition.lng ).then(
   response => {
    // console.log("res", response);
    const address = response.results[0].formatted_address,
     addressArray =  response.results[0].address_components,
     city = this.getCity( addressArray ),
     area = this.getArea( addressArray ),
     state = this.getState( addressArray );
     this.saveAddress(address)

    // console.log( 'city', city, area, state );
  
    this.setState( {
     address: ( address ) ? address : '',
     area: ( area ) ? area : '',
     city: ( city ) ? city : '',
     state: ( state ) ? state : '',
    } )
   },
   error => {
    console.error(error);
   }
  );
 };
/**
  * Component should only update ( meaning re-render ), when the user selects the address, or drags the pin
  *
  * @param nextProps
  * @param nextState
  * @return {boolean}
  */
 shouldComponentUpdate( nextProps, nextState ){
  if (
   this.state.markerPosition.lat !== this.props.center.lat ||
   this.state.address !== nextState.address
  //  this.state.city !== nextState.city ||
  //  this.state.area !== nextState.area ||
  //  this.state.state !== nextState.state
  ) {
   return true
  } else if ( this.props.center.lat === nextProps.center.lat ){
   return false
  }
 }
/**
  * Get the city and set the city input value to the one selected
  *
  * @param addressArray
  * @return {string}
  */
 getCity = ( addressArray ) => {
  let city = '';
  // console.log(addressArray);
  for( let i = 0; i < addressArray.length; i++ ) {
   if ( addressArray[ i ].types[0] && 'administrative_area_level_3' === addressArray[ i ].types[0] ) {
    city = addressArray[ i ].long_name;
    // console.log(city);
    return city;
   }
   
  }
 };
/**
  * Get the area and set the area input value to the one selected
  *
  * @param addressArray
  * @return {string}
  */
 getArea = ( addressArray ) => {
  let area = '';
  for( let i = 0; i < addressArray.length; i++ ) {
   if ( addressArray[ i ].types[0]  ) {
    for ( let j = 0; j < addressArray[ i ].types.length; j++ ) {
     if ( 'sublocality_level_1' === addressArray[ i ].types[j] || 'locality' === addressArray[ i ].types[j] ) {
      area = addressArray[ i ].long_name;
      return area;
     }
    }
   }
  }
 };
/**
  * Get the address and set the address input value to the one selected
  *
  * @param addressArray
  * @return {string}
  */
 getState = ( addressArray ) => {
  let state = '';
  for( let i = 0; i < addressArray.length; i++ ) {
   for( let i = 0; i < addressArray.length; i++ ) {
    if ( addressArray[ i ].types[0] && 'administrative_area_level_1' === addressArray[ i ].types[0] ) {
     state = addressArray[ i ].long_name;
     return state;
    }
   }
  }
 };
/**
  * And function for city,state and address input
  * @param event
  */
 onChange = ( event ) => {
  this.setState({ [event.target.name]: event.target.value });
 };
/**
  * This Event triggers when the marker window is closed
  *
  * @param event
  */
 onInfoWindowClose = ( event ) => {
};
/**
  * When the user types an address in the search box
  * @param place
  */
 onPlaceSelected = ( place ) => {
  // console.log("place", place);
const address = place.formatted_address,
   addressArray =  place.address_components,
   city = this.getCity( addressArray ),
   area = this.getArea( addressArray ),
   state = this.getState( addressArray ),
   latValue = place.geometry.location.lat(),
   lngValue = place.geometry.location.lng();
   this.saveAddress(address)

// Set these values in the state.
  this.setState({
   address: ( address ) ? address : '',
   area: ( area ) ? area : '',
   city: ( city ) ? city : '',
   state: ( state ) ? state : '',
   markerPosition: {
    lat: latValue,
    lng: lngValue
   },
   mapPosition: {
    lat: latValue,
    lng: lngValue
   },
   type : 'autocompelete',
  })
 };
/**
  * When the marker is dragged you get the lat and long using the functions available from event object.
  * Use geocode to get the address, city, area and state from the lat and lng positions.
  * And then set those values in the state.
  *
  * @param event
  */
 onMarkerDragEnd = ( center ) => {

  // console.log( 'event', event );
  // let newLat = event.latLng.lat(),
  //  newLng = event.latLng.lng(),
  //  addressArray = [];
  //  console.log(newLat, newLng); 
Geocode.fromLatLng( center.lat , center.lng ).then(
   response => {
    // console.log('res', response);
    const address = response.results[0].formatted_address,
     addressArray =  response.results[0].address_components,
     city = this.getCity( addressArray ),
     area = this.getArea( addressArray ),
     state = this.getState( addressArray );
     this.saveAddress(address)

this.setState( {
     address: ( address ) ? address : '',
     area: ( area ) ? area : '',
     city: ( city ) ? city : '',
     state: ( state ) ? state : '',

     markerPosition: {
      lat: center.lat,
      lng: center.lat
     },
     type : "marker"
    } )
   },
   error => {
    console.error(error);
   }
  );
  // console.log(this.state.mapPosition);
  // console.log(this.state.markerPosition);
 };

 accessGeolocation = async() => {
  if ("geolocation" in navigator) {
      // console.log("Available");
      this.displayCurrentLocation(this.state.newCurrentPosition.lat, this.state.newCurrentPosition.lng)
    } else {
      // console.log("Not Available");
    }
}
  displayCurrentLocation = (newLat, newLng) => {
    // console.log("map11",newLat, newLng  );
    Geocode.fromLatLng( newLat, newLng ).then(
      response => {
      // console.log("map2", newLat, newLng)
      //  console.log('res', response);
       const address = response.results[0].formatted_address,
        addressArray =  response.results[0].address_components,
        // console.log(addressArray);
        city = this.getCity( addressArray ),
        area = this.getArea( addressArray ),
        state = this.getState( addressArray );
        // console.log("map3", newLat);
        this.saveAddress(address)


      this.setState( {
        address: ( address ) ? address : '',
        area: ( area ) ? area : '',
        city: ( city ) ? city : '',
        state: ( state ) ? state : '',
        markerPosition: {
          lat: newLat,
          lng: newLng
         },
         type : "current"
       } )
      // console.log("map4", newLat);

      },
      // error => {
      //  console.error(error);
      // }
     );
  }
  newFunc = (place) => {
    // console.log(place.latLng.lat());
  }
  saveAddress = (address) => {
    localStorage.setItem("address", address)
  }
  // handleCenter = () => {    
  //   if (refMap?.state.map) {
  //     const mapCenter = refMap.state.map.center
  //     const newLat = mapCenter.lat()
  //     const newLng = mapCenter.lng()
      // console.log('googleMap2', refMap, mapCenter);
      // console.log("mapCenter", newLat, newLng);
  //     const center = { 
  //       lat :newLat,
  //       lng : newLng
  //     }
  //     setPositionMarker(center)
  //   }
  // }

render(){
let map;
  if( this.props.center.lat !== undefined ) {
   map = <div className='container'>
     <div>
      {/* <div className="form-group">
       <label htmlFor="">City</label>
       <input type="text" name="city" className="form-control" onChange={ this.onChange }  value={ this.state.city }/>
      </div> */}
      {/* <div className="form-group">
       <label htmlFor="">Area</label>
       <input type="text" name="area" className="form-control" onChange={ this.onChange }  value={ this.state.area }/>
      </div> */}
      {/* <div className="form-group">
       <label htmlFor="">State</label>
       <input type="text" name="state" className="form-control" onChange={ this.onChange }  value={ this.state.state }/>
      </div> */}
      <div className="form-group my-2">
       <label htmlFor="address" className="text-MyBase">العنوان</label>
       <input type="text" name="address" id='address' className="form-control" onChange={(e) => this.onChange(e) }  value={ this.state.address }/>
      </div>
     </div>
     <div className='w-100 position-relative' style={{ height : "100px"}} >
     
       <TestMap onMarkerDragEnd={this.onMarkerDragEnd} onPlaceSelected={this.onPlaceSelected} center={this.state.markerPosition} zoom={10} apiKeyGoogleMaps={apiKeyGoogleMaps}  type={this.state.type} />
       
       <button className='currentLocation-tap' onClick={this.accessGeolocation}><i className="fa-solid fa-location-crosshairs fs-5 m-2"></i></button>
       
     </div>
     
    </div>
} else {
   map = <div style={{height: this.props.height}} />
  }
  return( map )
 }
}
export default Map